import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useQuery } from "@apollo/react-hooks";
import { GET_GIT_TYPES } from "../queryGql";
import { Input } from "semantic-ui-react";
import _get from "lodash/get";
import _map from "lodash/map";
import _find from "lodash/find";
import { Label } from "semantic-ui-react";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  .git_input {
    margin-bottom: 10px;
  }
`;

const LabelContent = styled(Label)`
  width: 90px;
  text-align: left;
`;

const GitsInput = ({ gitInputs, onChange }) => {
  const { loading, error, data, refetch, networkStatus } = useQuery(
    GET_GIT_TYPES,
    {
      variables: {
        types: ["GIT_TYPE"],
        limit: 10000,
        offset: 0,
        sort: "codeRank",
        order: "desc"
      }
    }
  );
  const [inputs, setInputs] = useState(gitInputs);
  const gitLinks = _get(data, "codes.items", []);
  return (
    <Container>
      {_map(gitLinks, git => {
        const { id, name, value: gitValue } = git;
        const gitInput = _find(inputs, i => i.gitTypeId === id);
        const { linkUrl } = gitInput || {};
        return (
          <Input
            key={"git_" + id}
            className={"git_input"}
            label={<LabelContent>{name}</LabelContent>}
            placeholder={"Git"}
            defaultValue={linkUrl}
            onChange={(e, { name, value }) => {
              let newGitInputs = [];
              if (gitInput) {
                newGitInputs = _map(inputs, g => {
                  if (g.gitTypeId === id) {
                    return { ...g, linkUrl: value };
                  }
                  return g;
                });
              } else {
                newGitInputs = [...inputs, { gitTypeId: id, linkUrl: value }];
              }
              setInputs(newGitInputs);
              onChange(newGitInputs);
            }}
          />
        );
      })}
    </Container>
  );
};

export default GitsInput;
