import React, { useState, useEffect } from "react";
import styled from "styled-components";

const Container = styled.div`
  width: 100%;
  margin-bottom: 30px;
  display: flex;
  padding: 0 20px;
`;

const Title = styled.div`
  font-size: 18px;
  font-weight: bold;
`;

const Header = ({ title }) => {
  return (
    <Container>
      <Title>{title}</Title>
    </Container>
  );
};

export default Header;
