import React, { useState, useEffect } from "react";
import { Button, Icon } from "semantic-ui-react";
import styled from "styled-components";
import { PERSONAL_PROGRAM_CREATE } from "../../../../../common/CONSULT";
const CreateButton = styled(Button)``;

const CreateProgramButton = ({ onGoTo }) => {
  return (
    <CreateButton
      size={"mini"}
      basic
      circular
      color="blue"
      onClick={() => onGoTo(PERSONAL_PROGRAM_CREATE)}
    >
      <Icon name={"plus"} />
      
    </CreateButton>
  );
};

export default CreateProgramButton;
