import React, { useState, useEffect } from "react";
import styled from "styled-components";
import BookItem from "../../../common/item/BookItem";
import { _getByRank } from "../../../utils/utils";

const Container = styled.div`
  border-bottom: 1px solid
    ${props => (props.isLastOne ? "transparent" : "#cecece")};
  display: flex;
  align-items: center;
`;

const ContentBox = styled.div`
  height: 160px;
  display: flex;
  flex-direction: column;
  padding-bottom: 30px;
  margin-left: 6px;
`;

const Author = styled.div`
  font-size: 14px;
  color: gray;
  text-align: left;
  margin-bottom: 10px;
`;

const Intro = styled.div`
  flex: 1;
  text-align: left;
`;

const DateCreated = styled.div`
  text-align: left;
  color: gray;
  font-size: 12px;
`;
const BookListItem = ({ book, isLastOne }) => {
  const { creator, intro, dateCreated } = book;
  return (
    <Container isLastOne={isLastOne}>
      <BookItem size={0.7} book={book} />
      <ContentBox>
        <Author>
          &nbsp;&nbsp;&nbsp;&nbsp;
          {_getByRank(creator, ["nickname", "username"])}
        </Author>
        <Intro>&nbsp;&nbsp;&nbsp;&nbsp;{intro}</Intro>
        <DateCreated>
          &nbsp;&nbsp;&nbsp;&nbsp;{dateCreated}
        </DateCreated>
      </ContentBox>
    </Container>
  );
};

export default BookListItem;
