const TextField = styled.div``;

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const ResetPasswordInput = ({ onReset }) => {
  return (
    <Container>
      <TextField>********</TextField>
      <Button type={"link"} onClick={onReset}>
        
      </Button>
    </Container>
  );
};

export default ResetPasswordInput;
