import React, { useState, useEffect } from "react";
import styled from "styled-components";

import { Segment, Sidebar } from "semantic-ui-react";
import Header from "../../Header";
import { getRouteLastValue } from "../../../../../utils/routeUtils";
import _split from "lodash/split";
import _get from "lodash/get";
import _map from "lodash/map";
import { PERSONAL_PAGES } from "../../../../../common/CONSULT";
import PageItem from "../../../../QueryItem";
import { GET_PROGRAM } from "../../../../../common/queryGql";
import ProgramEditForm from "./ProgramEditForm";
import ProgramVersions from "./ProgramVersions";
import { clickEle } from "../../../../../utils/elementLink";
const Container = styled.div`
  .sidebar_push {
    border: none !important;
    padding-bottom: 30px;
  }
  .sidebar_pusher {
    border: none !important;
  }
  .side_bar {
    border: none !important;
    width: 600px !important;
    background-color: white;
  }
`;

const ContentBox = styled.div`
  min-height: 400px;
  padding-bottom: 30px;
`;

const useProgramEdit = ({}) => {
  const programId = _get(
    _split(getRouteLastValue(), PERSONAL_PAGES.PROGRAM_EDIT),
    "[1]"
  );
  const [visible, setVisible] = useState(false);
  return { programId, visible, setVisible };
};

const resolveProgramForm = program => {
  return {
    ...program,
    coverUrl: _get(program, "cover.url"),
    tags: _map(_get(program, "tags", []), t => t.name),
    gitInputs: _map(_get(program, "gitLinks", []), g => {
      return {
        id: g.id,
        gitTypeId: g.gitType.id,
        linkUrl: g.linkUrl,
        branch: g.branch
      };
    }),
    description: program.descriptionUrl
  };
};

const ProgramEdit = ({ onGoTo }) => {
  const { programId, visible, setVisible } = useProgramEdit({});
  return (
    <Container>
      <Sidebar.Pushable as={Segment} className={"sidebar_push"}>
        <Sidebar
          className={"side_bar"}
          animation="overlay"
          icon="labeled"
          direction={"right"}
          inverted={true}
          onHide={() => setVisible(false)}
          vertical={true}
          visible={visible}
          width="very wide"
        >
          <ProgramVersions
            programId={programId}
            onClose={() => setVisible(false)}
          />
        </Sidebar>

        <Sidebar.Pusher className={"sidebar_pusher"}>
          <Header title={""} />
          <ContentBox>
            <PageItem
              id={programId}
              autoLoading
              itemPath={"program"}
              gql={GET_PROGRAM}
            >
              {({ loading, refetch, item }) => {
                return (
                  <ProgramEditForm
                    sidebarVisible={visible}
                    initValue={resolveProgramForm(item)}
                    refetch={() => {
                      clickEle("refetch_program_versions");
                      return refetch();
                    }}
                    programId={programId}
                    onGoTo={onGoTo}
                    onShowVersions={() => setVisible(true)}
                  />
                );
              }}
            </PageItem>
          </ContentBox>
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </Container>
  );
};

export default ProgramEdit;
