import React, { useState, useEffect } from "react";
import { OBJ_TYPES } from "../../../../../common/CONSULT";
import Ups from "../Ups";

const AllUps = ({ currentUser }) => {
  return (
    <Ups
      types={[
        OBJ_TYPES.PROGRAM,
        OBJ_TYPES.BLOG,
        OBJ_TYPES.BOOK,
        OBJ_TYPES.CHAPTER,
        OBJ_TYPES.COMMENT
      ]}
      emptyText={""}
      currentUser={currentUser}
    />
  );
};

export default AllUps;
