import React, { useState, useEffect } from "react";
import { Upload, message } from "antd";
import { InboxOutlined } from "@ant-design/icons";
import styled from "styled-components";
import { SERVER_URL } from "../../../config";
import _get from "lodash/get";
import ModalHint from "../ModalHint";

const { Dragger } = Upload;

const Container = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  .uploader {
    width: 600px;
  }
`;

function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener("load", () => callback(reader.result));
  reader.readAsDataURL(img);
}

function beforeUpload(file, accept) {
  let fileType = accept.slice(1);
  if (fileType === "js") {
    fileType = "javascript";
  }
  const isSuchType = file.type.indexOf(fileType) >= 0;
  if (!isSuchType) {
    ModalHint.error(` ${fileType} !`);
  }
  const isLt2M = file.size / 1024 / 1024 < 10;
  if (!isLt2M) {
    ModalHint.error(" 10MB!");
  }
  return isSuchType && isLt2M;
}

const useFileInput = ({ isTmp, value, onChange }) => {
  const [loading, setLoading] = useState(false);
  const [fileUrl, setFileUrl] = useState(value);
  const handleChange = info => {
    if (info.file.status === "uploading") {
      setLoading(true);
    }
    if (info.file.status === "done") {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, fileUrl => {
        setLoading(false);
        setFileUrl(fileUrl);
        const ossFileUrl = _get(info.file, "response.pathArray[0]");
        onChange(ossFileUrl);
      });
    }
  };
  const onClear = () => {
    onChange("");
    setFileUrl(null);
  };
  return {
    loading,
    fileUrl,
    onClear,
    handleChange,
    actionUrl: SERVER_URL + (isTmp ? "/uploadTemp" : "/uploadm")
  };
};

const FileInput = ({
  isTmp = false,
  value,
  multiple = false,
  fileData,
  accept,
  onChange
}) => {
  const { loading, fileUrl, actionUrl, handleChange, onClear } = useFileInput({
    isTmp,
    value,
    onChange
  });
  const fileListProps =
    fileData && fileUrl === fileData.url
      ? { fileList: [{ ...fileData, id: -1, name: fileData.fileName }] }
      : {};
  return (
    <Container>
      <Dragger
        name="file"
        accept={accept}
        multiple={multiple}
        className="uploader"
        maxCount={1}
        //fileList={[fileData]}
        {...fileListProps}
        showUploadList={true}
        action={actionUrl}
        onRemove={file => {
          onClear();
        }}
        beforeUpload={file => beforeUpload(file, accept)}
        onChange={handleChange}
      >
        <>
          <p className="ant-upload-drag-icon">
            <InboxOutlined />
          </p>
          <p className="ant-upload-text"></p>
        </>
      </Dragger>
    </Container>
  );
};

export default FileInput;
