import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PaginationQueryList from "../../../../../common/PaginationQueryList";
import { GET_PROGRAMS } from "../../../../../common/queryGql";
import _map from "lodash/map";
import ProgramItem from "./ProgramItem";
import Empty from "../../../../../common/Empty";
import PersistStorage from "../../../../../utils/PersistStorage";
import { MY_PROGRAMS_REFRESH } from "../../../../../common/CONSULT";
import isEmpty from "validator/es/lib/isEmpty";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 14px;
  min-height: 200px;
`;

const _MyPrograms = ({ items, onGoTo, refetch }) => {
  // 
  const refreshKey = PersistStorage.getItem(MY_PROGRAMS_REFRESH);
  useEffect(() => {
    if (refreshKey !== "refresh") {
      refetch && refetch();
    } else {
      PersistStorage.setItem(MY_PROGRAMS_REFRESH, "refresh");
    }
  }, [refreshKey !== "refresh"]);
  return (
    <Container>
      {_map(items, item => {
        return (
          <ProgramItem
            key={"my_program_" + item.id}
            program={item}
            onEdit={() => onGoTo(`/personal/programEdit${item.id}`)}
            onToProgram={() => onGoTo(`/program/${item.id}`)}
          />
        );
      })}
    </Container>
  );
};

const MyPrograms = ({ currentUser, onGoTo, onUpdateTabCount }) => {
  if (!currentUser.id) {
    return <Empty text={""} />;
  }
  return (
    <PaginationQueryList
      gql={GET_PROGRAMS}
      searchFields={{
        creatorId: currentUser.id
      }}
      hidePaginationInSinglePage={true}
      paginationSize={"mini"}
      paginationSecondary={true}
      dataKey={"programs"}
      limit={10}
      onUpdateCount={onUpdateTabCount}
    >
      {({ loading, count, items, refetch }) => {
        if (count <= 0) {
          return <Empty text={""} />;
        }
        return <_MyPrograms items={items} refetch={refetch} onGoTo={onGoTo} />;
      }}
    </PaginationQueryList>
  );
};

export default MyPrograms;
