import React, { useState, useEffect } from "react";
import { Modal, Button } from "semantic-ui-react";
import PageItem from "../../../../QueryItem";
import EditVersionForm from "./EditVersionForm";
import { GET_PROGRAM_VERSION } from "../../../../../common/mutationGql";
import styled from "styled-components";
import { clickEle } from "../../../../../utils/elementLink";
import _get from "lodash/get";

const ModalContentBox = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
`;

const EditModal = ({
  open,
  onClose,
  programVersionId,
  programId,
  refetch: refetchProgram
}) => {
  const [saveLoading, setSaveLoading] = useState(false);
  const onCloseModal = e => {
    setSaveLoading(false);
    onClose(e);
  };
  return (
    <Modal
      style={{ top: "300px", width: "700px" }}
      dimmer={"blurring"}
      open={open}
      onClose={onCloseModal}
    >
      <Modal.Header>
        {programVersionId ? "" : ""}
      </Modal.Header>
      <Modal.Content>
        <ModalContentBox>
          {programVersionId ? (
            <PageItem
              id={programVersionId}
              gql={GET_PROGRAM_VERSION}
              autoLoading
              itemPath={"programVersion"}
            >
              {({ loading, refetch, item }) => {
                return (
                  <EditVersionForm
                    programId={programId}
                    loading={loading}
                    refetch={() => {
                      refetchProgram();
                    }}
                    initValue={{ ...item, codeUrl: _get(item, "codeFile.url") }}
                    onCloseModal={onCloseModal}
                    setLoading={setSaveLoading}
                  />
                );
              }}
            </PageItem>
          ) : (
            <EditVersionForm
              programId={programId}
              onCloseModal={onCloseModal}
              setLoading={setSaveLoading}
              initValue={{ importType: "OnlyJs" }}
              refetch={() => {
                refetchProgram();
              }}
            />
          )}
        </ModalContentBox>
      </Modal.Content>
      <Modal.Actions>
        <Button negative onClick={e => onCloseModal(e)}>
          
        </Button>
        <Button
          loading={saveLoading}
          positive
          onClick={() => clickEle("save_program_version")}
        >
          
        </Button>
      </Modal.Actions>
    </Modal>
  );
};

export default EditModal;
