import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Header from "../../Header";
import { Button, Input } from "semantic-ui-react";
import BasicForm from "../../../../../common/BasicForm";
import RichTextEditor from "../../../../../common/RichTextEditor";
import { useMutation } from "@apollo/react-hooks";
import { SAVE_PROGRAM } from "../../../../../common/mutationGql";
import _get from "lodash/get";
import {
  MY_PROGRAMS_REFRESH,
  PERSONAL_PROGRAM_EDIT
} from "../../../../../common/CONSULT";
import ImageInput from "../../../../../common/formFields/ImageInput";
import TagsInput from "../../../../../common/formFields/TagsInput";
import { clickEle } from "../../../../../utils/elementLink";
import PersistStorage from "../../../../../utils/PersistStorage";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ProgramCreate = ({ currentUser, onGoTo }) => {
  const [saveProgram, { data, loading: mLoading }] = useMutation(SAVE_PROGRAM, {
    onCompleted: result => {
      const id = _get(result, "saveProgram.id");
      PersistStorage.setItem(MY_PROGRAMS_REFRESH, Math.random().toString());
      onGoTo(`${PERSONAL_PROGRAM_EDIT}${id}`);
    }
  });

  return (
    <Container>
      <Header title={""} />
      <BasicForm
        width={800}
        items={[
          {
            label: "",
            required: true,
            fieldKey: "name",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <Input
                defaultValue={value}
                onChange={(e, { name, value }) => {
                  onChange(value);
                }}
              />
            )
          },
          {
            label: "",
            required: true,
            fieldKey: "coverUrl",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <ImageInput onChange={onChange} width={"140px"} height={"80"} />
            )
          },
          {
            label: "",
            fieldKey: "description",
            required: true,
            placeholder: ``,
            renderControl: ({
              item: { placeholder, fieldKey },
              value,
              onUpdateField
            }) => {
              return (
                <RichTextEditor
                  placeholder={placeholder}
                  fieldKey={fieldKey}
                  value={value}
                  onUpdateField={onUpdateField}
                />
              );
            }
          },
          {
            label: "",
            required: true,
            fieldKey: "tags",
            renderControl: ({ value, onChange }) => (
              <TagsInput onChange={onChange} />
            )
          }
        ]}
      >
        {({ form, onValidate }) => {
          return (
            <Button
              style={{ width: 200, marginTop: 20 }}
              onClick={() => {
                onValidate(() =>
                  saveProgram({
                    variables: {
                      name: form.name,
                      coverUrl: form.coverUrl,
                      description: form.description,
                      tags: form.tags
                    }
                  })
                );
              }}
              color="teal"
            >
              
            </Button>
          );
        }}
      </BasicForm>
    </Container>
  );
};

export default ProgramCreate;
