import React, { useState, useEffect } from "react";
import { OBJ_TYPES } from "../../../../../common/CONSULT";
import Ups from "../Ups";

const CommentUps = ({ currentUser }) => {
  return (
    <Ups
      type={OBJ_TYPES.COMMENT}
      currentUser={currentUser}
      emptyText={""}
    />
  );
};

export default CommentUps;
