import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Button } from "semantic-ui-react";

const UpdateButton = styled(Button)``;

const useUpdateButton = ({ onUpdateProgramStatus, status }) => {
  let buttonText = "";
  let actionStatus = "Awaiting";
  let color = "google plus";
  let successMsg = "";
  let errorMsg = "";
  switch (status) {
    case "Draft":
      buttonText = "";
      actionStatus = "Awaiting";
      color = "green";
      successMsg = "";
      errorMsg = "";
      break;
    case "Awaiting":
      buttonText = "";
      actionStatus = "Draft";
      successMsg = "";
      errorMsg = "";
      break;
    default:
      buttonText = "";
      actionStatus = "";
  }

  return { buttonText, actionStatus, color, successMsg, errorMsg };
};

const UpdateStatusButton = ({ status, onUpdateProgramStatus }) => {
  const {
    buttonText,
    actionStatus,
    color,
    successMsg,
    errorMsg
  } = useUpdateButton({
    status,
    onUpdateProgramStatus
  });
  return buttonText ? (
    <UpdateButton
      color={color}
      onClick={() => onUpdateProgramStatus(actionStatus, successMsg, errorMsg)}
    >
      {buttonText}
    </UpdateButton>
  ) : null;
};

export default UpdateStatusButton;
