import React, { useState } from "react";
import styled from "styled-components";
import { Icon } from "semantic-ui-react";
import { useMutation } from "@apollo/react-hooks";
import { SWITCH_UP_COMMENT } from "../../mutationGql";
import ModalHint from "../../ModalHint";
import ReportHint from "../../ReportHint";

const Container = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin-top: 10px;
  color: #99a2aa;
`;

const DateText = styled.div`
  margin-right: 10px;
`;

const ReplyButton = styled.div`
  cursor: pointer;
  margin-left: 10px;
`;

const MainActionsBox = styled.div`
  display: flex;
  align-items: center;
`;

const ReportButton = styled.div`
  cursor: pointer;
  font-size: 10px;
  margin-right: 10px;
`;

const ThumbsUp = styled(Icon)`
  cursor: pointer;
  margin-right: 4px !important;
  margin-bottom: 4px !important;
`;

const UpCount = styled.div``;

const useCommentActions = ({ upCount, currentUpped, currentReport }) => {
  const [uCount, setUCount] = useState(upCount);
  const [report, setReport] = useState(currentReport);
  const [cUpped, setCUpped] = useState(currentUpped);
  const [switchUpComment, { data, loading }] = useMutation(SWITCH_UP_COMMENT, {
    onCompleted: data => {
      const { switchUpComment } = data;
      setUCount(switchUpComment.upCount);
      setCUpped(switchUpComment.currentUpped);
    },
    onError: () => ModalHint.error("")
  });
  return {
    uCount,
    setUCount,
    cUpped,
    setCUpped,
    switchUpComment,
    report,
    setReport
  };
};

const CommentActions = ({
  commentId,
  dateCreated,
  onReply,
  upCount,
  currentUpped,
  currentReport
}) => {
  const {
    uCount,
    cUpped,
    switchUpComment,
    report,
    setReport
  } = useCommentActions({
    upCount,
    currentUpped,
    currentReport
  });
  return (
    <Container>
      <MainActionsBox>
        <DateText>{dateCreated}</DateText>
        <ThumbsUp
          color={cUpped ? "red" : ""}
          onClick={e => {
            e.stopPropagation();
            switchUpComment({ variables: { commentId } });
          }}
          name={cUpped ? "thumbs up" : "thumbs up outline"}
        />
        {uCount > 0 && <UpCount>{uCount}</UpCount>}
        {onReply && <ReplyButton onClick={onReply}></ReplyButton>}
      </MainActionsBox>
      <ReportButton
        onClick={e => {
          e.stopPropagation();
          ReportHint.show("Comment", commentId, report, {
            reportCallback: report => setReport(report)
          });
        }}
      >
        {report ? "" : ""}
      </ReportButton>
    </Container>
  );
};

export default CommentActions;
