import React from "react";
import PaginationQueryList from "../../../common/PaginationQueryList";
import { GET_FOLLOWERS } from "../../../common/queryGql";
import FlatList from "../../../common/FlatList";
import RefetchHandle from "../../../common/RefetchHandle";
import Empty from "../../../common/Empty";

const FollowsQuery = ({
  userId,
  type,
  types,
  children,
  columns = 4,
  queryLimit = 10,
  refetchNow,
  emptyText,
  beforeRefetch
}) => {
  return (
    <PaginationQueryList
      hidePaginationInSinglePage={true}
      gql={GET_FOLLOWERS}
      searchFields={{
        types: types ? types : [type],
        userId: userId,
        sort: "lastUpdated",
        order: "desc",
        limit: queryLimit
      }}
      paginationSize={"mini"}
      dataKey={"followers"}
      limit={queryLimit}
    >
      {({ loading, count, items, refetch }) => {
        if (count <= 0) {
          return <Empty text={emptyText} />;
        }
        return (
          <div>
            <FlatList items={items} columns={columns}>
              {({ item, isLastRow }) => {
                return children({ item, refetchFollows: refetch, isLastRow });
              }}
            </FlatList>
            <RefetchHandle
              refetch={() => {
                beforeRefetch && beforeRefetch();
                refetch();
              }}
              refetchOnce={refetchNow}
            />
          </div>
        );
      }}
    </PaginationQueryList>
  );
};

export default FollowsQuery;
