import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PaginationQueryList from "../../../../../common/PaginationQueryList";

import _map from "lodash/map";
import FollowsQuery from "../../FollowsQuery";
import { OBJ_TYPES } from "../../../../../common/CONSULT";
import ObjItem from "../../ObjItem";
import { followDataToJson } from "../../../../../utils/jsonUtils";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 14px;
  min-height: 200px;
`;

const AllFollowers = ({ currentUser }) => {
  return (
    <FollowsQuery
      columns={1}
      queryLimit={20}
      userId={currentUser.id}
      emptyText={""}
      types={[OBJ_TYPES.PROGRAM, OBJ_TYPES.BLOG]}
    >
      {({ item }) => {
        const obj = followDataToJson(item.followedObj);
        console.log("obj______________________", obj);
        return <ObjItem obj={obj} objType={item.objType} />;
      }}
    </FollowsQuery>
  );
};

export default AllFollowers;
