import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Button, Header, Icon, Input, Modal } from "semantic-ui-react";
import BasicForm from "../../../../common/BasicForm";
import ToastHint from "../../../../common/ToastHint";
import { useBindPhone, useSendCaptcha } from "../../../../common/hooks";
import { ACTION_TYPE, CAPTCHA_TYPE } from "../../../../common/CONSULT";
import CaptchaInput from "../../../../common/formFields/CaptchaInput";
import _get from "lodash/get";

const ButtonBox = styled.div`
  display: flex;
  justify-content: center;
`;

const BindPhoneModal = ({ open, onHide, title, onRefetch }) => {
  const { loading: mLoading, onBindPhone } = useBindPhone({
    onCompleted: res => {
      if (res.success) {
        onHide();
        onRefetch();
        ToastHint.showSuccess("");
      } else {
        ToastHint.showFailure("");
      }
    },
    onError: () => {
      ToastHint.showFailure("");
    }
  });
  return (
    <Modal
      style={{ top: 300 }}
      size={"mini"}
      closeIcon
      open={open}
      onClose={onHide}
    >
      <Header content={title} />
      <Modal.Content>
        <BasicForm
          loading={mLoading}
          items={[
            {
              label: "",
              required: true,
              fieldKey: "phone",
              rules: { phone: true },
              placeholder: ``,
              renderControl: ({ value, onChange }) => (
                <Input
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    onChange(value);
                  }}
                  placeholder={""}
                />
              )
            },
            {
              label: "",
              required: true,
              fieldKey: "captchaCode",
              placeholder: ``,
              renderControl: ({
                value,
                onChange,
                onValidate,
                form,
                error,
                formItemState,
                onUpdateState,
                onSetError
              }) => (
                <CaptchaInput
                  value={value}
                  error={error}
                  placeholder={""}
                  captchaType={CAPTCHA_TYPE.SMS}
                  actionType={ACTION_TYPE.BindPhoneNumber}
                  onChange={onChange}
                  state={formItemState}
                  onSetError={onSetError}
                  onUpdateState={onUpdateState}
                  onValidate={sendCaptcha =>
                    onValidate(() => sendCaptcha(_get(form, "phone")), [
                      "phone"
                    ])
                  }
                />
              )
            }
          ]}
        >
          {({ form, onValidate, onAddErrors }) => {
            return (
              <ButtonBox>
                <Button
                  loading={mLoading}
                  style={{ width: 200, marginTop: 20 }}
                  onClick={() => {
                    onValidate(() => onBindPhone(form.phone, form.captchaCode));
                  }}
                  color="teal"
                >
                  
                </Button>
              </ButtonBox>
            );
          }}
        </BasicForm>
      </Modal.Content>
    </Modal>
  );
};

export default BindPhoneModal;
