import React from "react";
import styled from "styled-components";
import { Icon } from "semantic-ui-react";
import _get from "lodash/get";

const AvatarBox = styled.div`
  border-radius: ${props => props.width / 2}px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: ${props => props.width}px;
  height: ${props => props.width}px;
  border: 1px solid grey;
  background-color: #cecece;
  .avatar_default_icon {
    margin: 0 !important;
  }
  cursor: ${props => props.cursor};
`;

const AvatarImg = styled.img`
  border-radius: ${props => props.width / 2}px;
  width: ${props => props.width}px;
  height: ${props => props.width}px;
`;

const Avatar = ({ url, width = 26, size = "small", cursor, avatar }) => {
  const avatarUrl = url || _get(avatar, "url");
  return (
    <AvatarBox width={width} cursor={cursor}>
      {avatarUrl ? (
        <AvatarImg src={avatarUrl} width={width} />
      ) : (
        <Icon
          className={"avatar_default_icon"}
          name={"user outline"}
          size={size}
        />
      )}
    </AvatarBox>
  );
};

export default Avatar;
