import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Header from "../Header";
import { followDataToJson } from "../../../../utils/jsonUtils";
import FollowProblems from "./segaments/FollowProblems";
import MyProblems from "./segaments/MyProblems";
import PageTabs from "../PageTabs";
import CreateProblemButton from "./segaments/CreateProblemButton";

const Container = styled.div`
  width: 100%;
`;

const PROBLEM_TABS = [
  { name: "Follow", label: "" },
  { name: "My", label: "" }
];

const PageContent = ({ currentTab, currentUser }) => {
  switch (currentTab.name) {
    case "Follow":
      return <FollowProblems currentUser={currentUser} />;
    case "My":
      return <MyProblems currentUser={currentUser} />;
  }
};

const Problem = ({ currentUser, onGoTo }) => {
  return (
    <Container>
      <Header title={""} />
      <PageTabs
        tabs={PROBLEM_TABS}
        renderActions={() => <CreateProblemButton onGoTo={onGoTo} />}
      >
        {({ currentTab }) => {
          return (
            <PageContent currentTab={currentTab} currentUser={currentUser} />
          );
        }}
      </PageTabs>
    </Container>
  );
};

export default Problem;
