import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Header from "../Header";
import PageTabs from "../PageTabs";
import RelatedComments from "./segaments/RelatedComments";
import MyComments from "./segaments/MyComments";

const Container = styled.div`
  width: 100%;
`;

const COMMENT_TABS = [
  { name: "Related", label: "" },
  { name: "My", label: "" }
];

const PageContent = ({ currentTab, currentUser }) => {
  switch (currentTab.name) {
    case "Related":
      return <RelatedComments currentUser={currentUser} />;
    case "My":
      return <MyComments currentUser={currentUser} />;
  }
};

const Comment = ({ currentUser }) => {
  return (
    <Container>
      <Header title={""} />
      <PageTabs tabs={COMMENT_TABS}>
        {({ currentTab }) => {
          return (
            <PageContent currentUser={currentUser} currentTab={currentTab} />
          );
        }}
      </PageTabs>
    </Container>
  );
};

export default Comment;
