import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PaginationQueryList from "../../../../../common/PaginationQueryList";
import _map from "lodash/map";
import ProgramVersion from "./ProgramVerson";
import { GET_PROGRAM_VERSIONS } from "../../../../../common/queryGql";
import { Button, Header } from "semantic-ui-react";
import RefetchHandle from "../../../../../common/RefetchHandle";

const SlideHeader = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 20px 20px 0px;
  align-items: center;
`;

const Container = styled.div`
  margin-bottom: 20px;
`;

const Title = styled.div`
  font-size: 20px;
  font-weight: bold;
`;

const ProgramVersions = ({ programId, onClose }) => {
  return (
    <PaginationQueryList
      gql={GET_PROGRAM_VERSIONS}
      searchFields={{ programId }}
      hidePaginationInSinglePage={true}
      dataKey={"programVersions"}
      paginationSize={"mini"}
      paginationSecondary={true}
      limit={10}
    >
      {({ loading, error, refetch, items, count }) => {
        return (
          <Container>
            <SlideHeader>
              <Title>({count})</Title>{" "}
              <Button
                basic
                circular
                onClick={onClose}
                color="gray"
                icon="close"
              />
            </SlideHeader>
            {_map(items, item => {
              return (
                <ProgramVersion
                  key={"program_version_" + item.id}
                  refetch={refetch}
                  item={item}
                  programId={programId}
                  items={items}
                  totalCount={count}
                />
              );
            })}
            <RefetchHandle id={"refetch_program_versions"} refetch={refetch} />
          </Container>
        );
      }}
    </PaginationQueryList>
  );
};

export default ProgramVersions;
