import React from "react";
import styled from "styled-components";
import _get from "lodash/get";
import { Icon } from "semantic-ui-react";
import { BLOG } from "../CONSULT";
import Avatar from "../Avatar";

const Container = styled.div`
  position: relative;
  display: flex;
  cursor: pointer;
  &:hover {
  }
  width: 100%;
  margin-bottom: 18px;
  padding-bottom: 10px;
  border-bottom: 1px solid ${props => (props.isLastOne ? "none" : "#cecece")};
`;

const ImageBox = styled.div`
  margin-right: 10px;
`;

const CoverImage = styled.img``;

const Cover = ({ url }) => {
  return (
    <ImageBox>
      <CoverImage src={url} width={240} height={160} />
    </ImageBox>
  );
};

const FooterBox = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 6px;
`;

const FooterItem = styled.div`
  display: flex;
  align-items: center;
  margin-right: 20px;
`;

const Text = styled.div`
  margin-left: 16px;
`;

const Footer = ({ item }) => {
  return (
    <FooterBox>
      <FooterItem>
        <Avatar url={_get(item, "creator.avatar.url", "")} />
        <Text>{_get(item, "creator.username", "-username-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"calendar alternate outline"} />
        <Text>{_get(item, "dateCreated", "-date-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"heart outline"} />
        <Text>{_get(item, "upCount", "-like-")}</Text>
      </FooterItem>
      <FooterItem>
        <Icon name={"eye"} />
        <Text>{_get(item, "readCount", "-read-")}</Text>
      </FooterItem>
    </FooterBox>
  );
};

const ContentBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 6px;
`;

const Title = styled.div`
  font-size: 18px;
  color: #333333;
`;

const Description = styled.div`
  flex: 1;
`;

const BlogItem = ({ item, onGoTo, isLastOne, width }) => {
  const { id, cover, title, description } = item;
  return (
    <Container
      isLastOne={isLastOne}
      style={{ width }}
      onClick={() => onGoTo && onGoTo(`${BLOG}${id}`)}
    >
      <Cover url={_get(cover, "url")} />
      <ContentBox>
        <Title>{title}</Title>
        <Description>{description}</Description>
        <Footer item={item} />
      </ContentBox>
    </Container>
  );
};

export default BlogItem;
