import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { OBJ_TYPES } from "../../../common/CONSULT";
import ProgramItem from "./program/segaments/ProgramItem";
import BlogItem from "../../../common/item/BlogItem";
import LinkToObject from "../../../common/LinkToObject";
import CommentItem from "./comment/segaments/CommentItem";
import BookmarkItem from "./chapter/segaments/BookmarkItem";
import BookListItem from "./BookListItem";

const Container = styled.div`
  width: 100%;
  padding: 20px 40px;
`;

const getObjByObjType = ({ objType, obj, follower, isLastOne }) => {
  switch (objType) {
    case OBJ_TYPES.PROGRAM:
      return <ProgramItem program={obj} noActions={true} />;
    case OBJ_TYPES.BLOG:
      return <BlogItem item={obj} isLastOne={isLastOne} />;
    case OBJ_TYPES.COMMENT:
      return <CommentItem item={obj} isLastOne={isLastOne} />;
    case OBJ_TYPES.CHAPTER:
      return <BookmarkItem chapter={obj} noActions={true} />;
    case OBJ_TYPES.BOOK:
      return <BookListItem book={obj} isLastOne={isLastOne} />;
  }
};

const ObjItem = ({ objType, obj, dataObj, isLastOne }) => {
  const content = getObjByObjType({ objType, obj, dataObj, isLastOne });
  return (
    <Container>
      <LinkToObject objId={obj.id} objType={objType}>
        {content}
      </LinkToObject>
    </Container>
  );
};

export default ObjItem;
