import React, { useState, useRef, useEffect } from "react";
import styled from "styled-components";
import { Tag, Input, Tooltip, Form } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import _filter from "lodash/filter";
import _trim from "lodash/trim";

const useTagsInput = ({
  value,
  onUpdateField,
  onChange,
  fieldKey,
  maxTags
}) => {
  const [tags, setTags] = useState(value || []);
  const [inputVisible, setInputVisible] = useState(false);
  const [inputValue, setInputValue] = useState("");
  const [editInputIndex, setEditInputIndex] = useState(-1);
  const [editInputValue, setEditInputValue] = useState("");

  const saveEditInputRef = useRef(null);
  const saveInputRef = useRef(null);

  const removeTag = removedTag => {
    const removedTags = _filter(tags, tag => tag !== removedTag);
    setTags(removedTags);
    onUpdateField && onUpdateField(fieldKey, removedTags);
    onChange && onChange(removedTags);
  };
  const showInput = () => {
    setInputVisible(true);
  };

  useEffect(() => {
    saveInputRef.current && saveInputRef.current.focus();
  }, [inputVisible]);

  useEffect(() => {
    saveEditInputRef &&
      saveEditInputRef.current &&
      saveEditInputRef.current.focus();
  }, [editInputIndex]);

  const handleInputChange = e => {
    setInputValue(e.target.value);
  };

  const handleInputConfirm = () => {
    const trimValue = _trim(inputValue);
    if (trimValue && tags.indexOf(trimValue) === -1) {
      const newTags = [...tags, trimValue];
      setTags(newTags);
      onUpdateField && onUpdateField(fieldKey, newTags);
      onChange && onChange(newTags);
      if (newTags.length >= maxTags) {
        setInputVisible(false);
      }
    }
    setInputValue("");
    setInputVisible(false);
  };

  const handleEditInputChange = e => {
    setEditInputValue(e.target.value);
  };

  const handleEditInputConfirm = () => {
    const trimValue = _trim(editInputValue);
    if (trimValue && tags.indexOf(trimValue) === -1) {
      const newTags = [...tags];
      newTags[editInputIndex] = editInputValue;
      setTags(newTags);
      onUpdateField && onUpdateField(fieldKey, newTags);
      onChange && onChange(newTags);
      if (newTags.length > maxTags) {
        setInputVisible(false);
      }
    }
    setEditInputValue("");
    setEditInputIndex(-1);
  };

  return {
    tags,
    inputVisible,
    inputValue,
    editInputIndex,
    editInputValue,
    setEditInputIndex,
    setInputValue,
    saveEditInputRef,
    saveInputRef,
    setEditInputValue,
    removeTag,
    showInput,
    handleInputChange,
    handleInputConfirm,
    handleEditInputChange,
    handleEditInputConfirm
  };
};

const TagsContainer = styled.div`
  display: flex;
  align-items: center;
  height: 60px;
  flex-wrap: wrap;
  .tag-input {
    min-width: 78px !important;
    max-width: 120px !important;
    margin-right: 8px;
    vertical-align: top;
  }
  .site-tag-plus {
    background: #fff;
    border-style: dashed;
    cursor: pointer;
    padding: 4px 6px;
    font-size: 18px;
  }
  .edit-tag {
    cursor: pointer;
    user-select: none;
    padding: 4px 6px;
    font-size: 18px;
  }
`;

const TagsInput = ({
  placeholder,
  formItemProps,
  disabled,
  addTitle = "",
  fieldKey,
  onChange,
  onUpdateField,
  value,
  maxTags = 5
}) => {
  const {
    tags,
    inputVisible,
    inputValue,
    editInputIndex,
    editInputValue,
    removeTag,
    showInput,
    handleInputChange,
    handleInputConfirm,
    handleEditInputChange,
    handleEditInputConfirm,
    setEditInputIndex,
    setEditInputValue,
    saveEditInputRef,
    saveInputRef
  } = useTagsInput({ fieldKey, onUpdateField, onChange, value, maxTags });
  const tagsInput = (
    <TagsContainer>
      {tags.map((tag, index) => {
        if (editInputIndex === index && !disabled) {
          return (
            <Input
              ref={saveEditInputRef}
              key={tag}
              size="small"
              className="tag-input"
              value={editInputValue}
              onChange={handleEditInputChange}
              onBlur={handleEditInputConfirm}
              onPressEnter={handleEditInputConfirm}
            />
          );
        }

        const isLongTag = tag.length > 20;

        const tagElem = (
          <Tag
            className="edit-tag"
            key={tag}
            closable={!disabled}
            onClose={() => removeTag(tag)}
          >
            <span
              aria-disabled={disabled}
              onDoubleClick={e => {
                setEditInputIndex(index);
                setEditInputValue(tag);
                e.preventDefault();
              }}
            >
              {isLongTag ? `${tag.slice(0, 20)}...` : tag}
            </span>
          </Tag>
        );
        return isLongTag ? (
          <Tooltip title={tag} key={tag}>
            {tagElem}
          </Tooltip>
        ) : (
          tagElem
        );
      })}
      {inputVisible && !disabled && tags.length < maxTags && (
        <Input
          ref={saveInputRef}
          type="text"
          size="small"
          className="tag-input"
          value={inputValue}
          onChange={handleInputChange}
          onBlur={handleInputConfirm}
          onPressEnter={handleInputConfirm}
        />
      )}
      {!inputVisible && !disabled && (
        <Tag className="site-tag-plus" onClick={showInput}>
          <PlusOutlined /> {addTitle}
        </Tag>
      )}
    </TagsContainer>
  );
  return formItemProps ? (
    <Form.Item {...formItemProps}>{tagsInput}</Form.Item>
  ) : (
    tagsInput
  );
};

export default TagsInput;
