import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Button } from "antd";
import { MAIN_RED } from "../Styles";
import ModalHint from "../ModalHint";
import { useMutation } from "@apollo/react-hooks";
import { UNBIND_EMAIL } from "../mutationGql";
import ToastHint from "../ToastHint";

const TextField = styled.div`
  color: ${props => (props.hasValue ? "" : "#cecece")};
`;

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const BindEmailInput = ({
  value,
  placeholder = "",
  onBindCompleted,
  onBind
}) => {
  const [onUnbindEmail, { data, loading }] = useMutation(UNBIND_EMAIL, {
    onCompleted: ({unbindEmail:{success,message}}) => {
      if(success){
        onBindCompleted && onBindCompleted();
        ToastHint.showSuccess("");
      }else{
        ToastHint.showFailure(message);
      }

    },
    onError: () => {
      ToastHint.showFailure("");
    }
  });
  return (
    <Container>
      <TextField hasValue={value}>{value ? value : placeholder}</TextField>
      {value ? (
        <Button
          type={"link"}
          onClick={() =>
            ModalHint.confirm(
              {
                title: "",
                content: "",
                loading
              },
              onHide =>
                onUnbindEmail().then(() => {
                  onHide();
                })
            )
          }
          style={{ color: "grey" }}
        >
          
        </Button>
      ) : (
        <Button type={"link"} onClick={onBind}>
          
        </Button>
      )}
    </Container>
  );
};

export default BindEmailInput;
