import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PageTabs from "../PageTabs";
import Header from "../Header";
import { OBJ_TYPES } from "../../../../common/CONSULT";
import AllFollowers from "./segaments/AllFollowers";
import ProgramFollowers from "./segaments/ProgramFollowers";
import BlogFollowers from "./segaments/BlogFollowers";

const Container = styled.div`
  width: 100%;
`;

const FOLLOWER_TABS = [
  { name: "All", label: "" },
  { name: OBJ_TYPES.PROGRAM, label: "" },
  { name: OBJ_TYPES.BLOG, label: "" }
];

const PageContent = ({ currentTab, currentUser }) => {
  switch (currentTab.name) {
    case "All":
      return <AllFollowers currentUser={currentUser} />;
    case OBJ_TYPES.PROGRAM:
      return <ProgramFollowers currentUser={currentUser} />;
    case OBJ_TYPES.BLOG:
      return <BlogFollowers currentUser={currentUser} />;
  }
};

const Follower = ({ currentUser }) => {
  return (
    <Container>
      <Header title={""} />
      <PageTabs tabs={FOLLOWER_TABS}>
        {({ currentTab }) => {
          return (
            <PageContent currentTab={currentTab} currentUser={currentUser} />
          );
        }}
      </PageTabs>
    </Container>
  );
};

export default Follower;
