import React, { useState, useEffect } from "react";
import FollowsQuery from "../../FollowsQuery";
import { OBJ_TYPES } from "../../../../../common/CONSULT";
import ProblemItem from "../../../../../common/item/ProblemItem";
import { followDataToJson } from "../../../../../utils/jsonUtils";

const FollowProblems = ({ currentUser }) => {
  return (
    <FollowsQuery
      emptyText={""}
      type={OBJ_TYPES.PROBLEM}
      userId={currentUser.id}
      columns={1}
    >
      {({ item }) => {
        const problem = followDataToJson(item.followedObj);
        return <ProblemItem problem={problem} />;
      }}
    </FollowsQuery>
  );
};

export default FollowProblems;
