import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Button, Header, Icon, Input, Modal, Select } from "semantic-ui-react";
import BasicForm from "../../../../common/BasicForm";
import ToastHint from "../../../../common/ToastHint";
import { useResetPassword } from "../../../../common/hooks";
import { ACTION_TYPE, CAPTCHA_TYPE } from "../../../../common/CONSULT";
import CaptchaInput from "../../../../common/formFields/CaptchaInput";
import _get from "lodash/get";
import isEmail from "validator/lib/isEmail";
import isPhone from "validator/lib/isMobilePhone";
const ButtonBox = styled.div`
  display: flex;
  justify-content: center;
`;

const ResetPasswordModal = ({
  email,
  phone,
  open,
  onHide,
  title,
  onRefetch
}) => {
  let options = [];
  if (email) {
    options.push({ text: email, value: email });
  }
  if (phone) {
    options.push({ text: phone, value: phone });
  }
  const { loading: mLoading, onResetPassword } = useResetPassword({
    onCompleted: res => {
      if (res.success) {
        onHide();
        onRefetch();
        ToastHint.showSuccess("");
      } else {
        ToastHint.showFailure("");
      }
    },
    onError: () => {
      ToastHint.showFailure("");
    }
  });
  return (
    <Modal
      style={{ top: 300 }}
      size={"mini"}
      closeIcon
      open={open}
      onClose={onHide}
    >
      <Header content={title} />
      <Modal.Content>
        <BasicForm
          loading={mLoading}
          formData={{ name: email || phone }}
          items={[
            {
              label:
                options.length > 1 ? " |" : email ? "" : "",
              required: true,
              fieldKey: "name",
              renderControl: ({ value, onChange }) => (
                <Select
                  disabled={options.length <= 1}
                  options={options}
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    console.log("value____GGDSFSDFDS______", value);
                    onChange(value);
                  }}
                  placeholder={``}
                />
              )
            },
            {
              label: "",
              required: true,
              fieldKey: "captchaCode",
              renderControl: ({
                value,
                onChange,
                onValidate,
                form,
                error,
                formItemState,
                onUpdateState,
                onSetError,
                setErrors
              }) => (
                <CaptchaInput
                  value={value}
                  error={error}
                  placeholder={""}
                  captchaType={
                    isEmail(_get(form, "name", ""))
                      ? CAPTCHA_TYPE.EMAIL
                      : CAPTCHA_TYPE.SMS
                  }
                  actionType={
                    isEmail(_get(form, "name", ""))
                      ? ACTION_TYPE.BindEmail
                      : ACTION_TYPE.BindPhoneNumber
                  }
                  onChange={onChange}
                  state={formItemState}
                  onSetError={onSetError}
                  onUpdateState={onUpdateState}
                  onValidate={sendCaptcha => {
                    const name = _get(form, "name");
                    if (!name) {
                      setErrors({ name: "" });
                      return;
                    }
                    if (!(isEmail(name) || isPhone(name))) {
                      setErrors({
                        name: ""
                      });
                      return;
                    }
                    onValidate(() => sendCaptcha(_get(form, "name")), ["name"]);
                  }}
                />
              )
            },
            {
              label: "",
              required: true,
              fieldKey: "password",
              renderControl: ({ value, onChange }) => (
                <Input
                  type={"password"}
                  autoComplete="new-password"
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    onChange(value);
                  }}
                  placeholder={``}
                />
              )
            },
            {
              label: "",
              required: true,
              fieldKey: "confirmPassword",
              renderControl: ({ value, onChange }) => (
                <Input
                  type={"password"}
                  autoComplete="new-password"
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    onChange(value);
                  }}
                  placeholder={``}
                />
              )
            }
          ]}
        >
          {({ form, onValidate, onAddErrors }) => {
            return (
              <ButtonBox>
                <Button
                  loading={mLoading}
                  style={{ width: 200, marginTop: 20 }}
                  onClick={() => {
                    onValidate(() => {
                      if (form.password !== form.confirmPassword) {
                        onAddErrors({
                          confirmPassword: ""
                        });
                        return;
                      }
                      onResetPassword(
                        form.name,
                        isEmail(form.name)
                          ? CAPTCHA_TYPE.EMAIL
                          : CAPTCHA_TYPE.SMS,
                        form.password,
                        form.captchaCode
                      );
                    });
                  }}
                  color="teal"
                >
                  
                </Button>
              </ButtonBox>
            );
          }}
        </BasicForm>
      </Modal.Content>
    </Modal>
  );
};

export default ResetPasswordModal;
