import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Modal, Button, Icon } from "semantic-ui-react";
import EditModalTrigger from "./EditModalTrigger";
import { Button as AntButton } from "antd";
import Space from "../../../../../common/Space";

const Container = styled.div`
  display: flex;
  align-items: center;
  background-color: transparent;
  justify-content: space-between;
`;

const ContentBox = styled.div`
  display: flex;
  align-items: center;
`;

const VersionBox = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 20px;
  font-size: 16px;
`;

const VersionText = styled.div`
  color: gray;
`;

const EditButton = styled.div`
  display: block;
  margin-left: 4px;
`;

const NewButton = styled(Button)``;

const AllVersion = styled(AntButton)``;

const CurrentVersion = ({
  onShowVersions,
  currentVersion,
  programId,
  refetch
}) => {
  return (
    <Container>
      <ContentBox>
        {currentVersion && (
          <EditModalTrigger
            refetch={refetch}
            programId={programId}
            editProgramVersionId={currentVersion.id}
          >
            <VersionBox>
              <VersionText>
                {currentVersion.name}:{currentVersion.versionCode}
              </VersionText>

              <EditButton>
                <Icon name={"edit"} />
              </EditButton>
            </VersionBox>
          </EditModalTrigger>
        )}
        <Space width={"20px"} />
        <EditModalTrigger refetch={refetch} programId={programId}>
          <NewButton size={"mini"} basic circular color="blue">
            <Icon name={"plus"} />
            
          </NewButton>
        </EditModalTrigger>
      </ContentBox>
      <AllVersion onClick={onShowVersions} type={"link"}>
        
        <Icon name={"angle double right"} />
      </AllVersion>
    </Container>
  );
};

export default CurrentVersion;
