import React from "react";
import styled from "styled-components";
import _get from "lodash/get";

import CommentActions from "../../../../../common/comment/segaments/CommentActions";
import Avatar from "../../../../../common/Avatar";
import { _getByRank } from "../../../../../utils/utils";
import RepliedComment from "./RepliedComment";
import LinkToObject from "../../../../../common/LinkToObject";
import CommentObj from "./CommentObj";

const Container = styled.div`
  width: 100%;
  display: flex;
  margin-bottom: 24px;
  padding-bottom: 10px;
  &:hover {
    .comment_obj {
      visibility: visible;
    }
  }
  border-bottom: 1px solid
    ${props => (props.isLastOne ? "transparent" : "#e5e9ef")};
`;

const AvatarBox = styled.div`
  width: 80px;
`;

const ContentBox = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

const UserInfoBox = styled.div`
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const Name = styled.div`
  color: #6d757a;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  cursor: pointer;
  &:hover {
    color: lightskyblue;
  }
`;

const InfoLeftBox = styled.div`
  display: flex;
  align-items: center;
`;

const UserInfo = ({ creator, repliedComment, commentObj }) => {
  return (
    <UserInfoBox>
      <InfoLeftBox>
        <Name>{_getByRank(creator, ["nickname", "username"])}</Name>
        <RepliedComment repliedComment={repliedComment} />
      </InfoLeftBox>
      <CommentObj  commentObj={commentObj} />
    </UserInfoBox>
  );
};

const ContentInfo = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 10px;
`;

const Content = styled.div`
  text-align: left;
`;

const CommentItem = ({ item, isLastOne }) => {
  const {
    creator,
    content,
    dateCreated,
    id,
    objId,
    type,
    currentUpped,
    upCount,
    currentReport,
    commentObj,
    repliedComment
  } = item;
  return (
    <LinkToObject objId={objId} objType={type}>
      <Container isLastOne={isLastOne}>
        <AvatarBox>
          <Avatar
            size={"large"}
            width={50}
            avatar={_get(creator, "avatar.url")}
          />
        </AvatarBox>
        <ContentBox>
          <ContentInfo>
            <UserInfo
              creator={creator}
              repliedComment={repliedComment}
              commentObj={commentObj}
            />
            <Content>{content}</Content>
            <CommentActions
              dateCreated={dateCreated}
              currentUpped={currentUpped}
              upCount={upCount}
              commentId={id}
              currentReport={currentReport}
            />
          </ContentInfo>
        </ContentBox>
      </Container>
    </LinkToObject>
  );
};

export default CommentItem;
