import React from "react";
import PaginationQueryList from "../../../../common/PaginationQueryList";
import FlatList from "../../../../common/FlatList";
import RefetchHandle from "../../../../common/RefetchHandle";
import { GET_UPS } from "../../../../common/queryGql";
import Empty from "../../../../common/Empty";

const UpsQuery = ({
  userId,
  type,
  types,
  children,
  columns = 4,
  queryLimit = 20,
  refetchNow,
  beforeRefetch,
  emptyText
}) => {
  return (
    <PaginationQueryList
      hidePaginationInSinglePage={true}
      gql={GET_UPS}
      searchFields={{
        types: types ? types : [type],
        userId: userId,
        sort: "lastUpdated",
        order: "desc",
        limit: queryLimit
      }}
      paginationSize={"mini"}
      dataKey={"ups"}
      limit={queryLimit}
    >
      {({ loading, count, items, refetch }) => {
        if (count <= 0) {
          return <Empty text={emptyText} />;
        }
        return (
          <div>
            <FlatList items={items} columns={columns}>
              {({ item, isLastRow }) => {
                return children({ item, refetchFollows: refetch, isLastRow });
              }}
            </FlatList>
            <RefetchHandle
              refetch={() => {
                beforeRefetch && beforeRefetch();
                refetch();
              }}
              refetchOnce={refetchNow}
            />
          </div>
        );
      }}
    </PaginationQueryList>
  );
};

export default UpsQuery;
