import React from "react";
import styled from "styled-components";
import { _getByRank } from "../../../../../utils/utils";

const Container = styled.div`
  display: flex;
  align-items: center;
  margin-left: 8px;
`;

const CommentName = styled.div`
  color: gray;
  margin-left: 6px;
  font-size: 16px;
  cursor: pointer;
  &:hover {
    color: lightskyblue;
  }
`;

const ReplyText = styled.div``;

const RepliedComment = ({ repliedComment }) => {
  if (!repliedComment) {
    return null;
  }
  const { creator } = repliedComment;
  return (
    <Container>
      <ReplyText></ReplyText>
      <CommentName>
        @{_getByRank(creator, ["nickname", "username"])}
      </CommentName>
    </Container>
  );
};

export default RepliedComment;
