import React, { useState, useEffect } from "react";

import styled from "styled-components";
import { Button } from "antd";
import { MAIN_RED } from "../Styles";
import ModalHint from "../ModalHint";
import { useMutation } from "@apollo/react-hooks";
import { UNBIND_PHONE } from "../mutationGql";
import ToastHint from "../ToastHint";

const TextField = styled.div`
  color: ${props => (props.hasValue ? "" : "#cecece")};
`;

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const BindPhoneInput = ({
  value,
  placeholder = "",
  onBindCompleted,
  onBind
}) => {
  const [onUnbindPhone, { data, loading }] = useMutation(UNBIND_PHONE, {
    onCompleted: ({unbindPhone:{success, message}}) => {
      if(success){
        onBindCompleted && onBindCompleted();
        ToastHint.showSuccess("");
      }else{
        ToastHint.showFailure(message)
      }
    },
    onError: () => {
      ToastHint.showFailure("");
    }
  });
  return (
    <Container>
      <TextField hasValue={value}>{value ? value : placeholder}</TextField>
      {value ? (
        <Button
          onClick={() =>
            ModalHint.confirm(
              {
                title: "",
                loading,
                content: ""
              },
              onHide => {
                onUnbindPhone().then(() => {
                  onHide();
                });
              }
            )
          }
          type={"link"}
          style={{ color: "grey" }}
        >
          
        </Button>
      ) : (
        <Button type={"link"} onClick={onBind}>
          
        </Button>
      )}
    </Container>
  );
};

export default BindPhoneInput;
