import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useSwitchFollow } from "../../../common/hooks";
import { OBJ_TYPES } from "../../../common/CONSULT";
import { Icon, Popup } from "semantic-ui-react";

const ItemWrapper = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  &:hover {
    .remove_btn {
      visibility: visible;
    }
  }
`;

const RemoveBox = styled.div`
  color: gray;
  display: flex;
  width: 100%;
  justify-content: flex-end;
  visibility: hidden;
  cursor: pointer;
  margin-bottom: 4px;
  &:hover {
    color: lightskyblue;
  }
`;

const FollowItemWrapper = ({
  objId,
  onSwitchFollowCompleted,
  successMsg = "",
  errorMsg = "",
  popupContent,
  children
}) => {
  const { onSwitchFollow, mLoading } = useSwitchFollow({
    objId: objId,
    objType: OBJ_TYPES.BOOK,
    onSwitchCompleted: onSwitchFollowCompleted,
    currentFollowed: true,
    successMsg: { unFollowed: successMsg },
    errorMsg: { unFollowed: errorMsg }
  });

  return (
    <ItemWrapper>
      <RemoveBox className={"remove_btn"}>
        <Popup
          content={popupContent}
          trigger={
            <Icon loading={mLoading} onClick={onSwitchFollow} name={"close"} />
          }
        />
      </RemoveBox>
      {children}
    </ItemWrapper>
  );
};

export default FollowItemWrapper;
