import React from "react";
import styled from "styled-components";
import PaginationQueryList from "../../../../../common/PaginationQueryList";
import { GET_FOLLOWERS } from "../../../../../common/queryGql";
import { OBJ_TYPES } from "../../../../../common/CONSULT";
import FlatList from "../../../../../common/FlatList";
import ProgramItem from "../../../../../common/item/ProgramItem";
import { followDataToJson } from "../../../../../utils/jsonUtils";
import Empty from "../../../../../common/Empty";
import LinkToObject from "../../../../../common/LinkToObject";

const Container = styled.div``;

const FollowPrograms = ({ currentUser, onUpdateTabCount }) => {
  return (
    <PaginationQueryList
      hidePaginationInSinglePage={true}
      gql={GET_FOLLOWERS}
      searchFields={{
        types: [OBJ_TYPES.PROGRAM],
        userId: currentUser.id,
        sort: "lastUpdated",
        order: "desc"
      }}
      paginationSize={"mini"}
      dataKey={"followers"}
      limit={10}
      onUpdateCount={onUpdateTabCount}
    >
      {({ loading, count, items, refetch }) => {
        if (count <= 0) {
          return <Empty text={""} />;
        }
        return (
          <Container>
            <FlatList items={items} columns={4}>
              {({ item }) => {
                const program = followDataToJson(item.followedObj);
                return (
                  <LinkToObject objType={OBJ_TYPES.PROGRAM} objId={program.id}>
                    <ProgramItem program={program} />
                  </LinkToObject>
                );
              }}
            </FlatList>
          </Container>
        );
      }}
    </PaginationQueryList>
  );
};

export default FollowPrograms;
