import React, { useState, useEffect } from "react";
import { Icon, Menu, Segment } from "semantic-ui-react";
import styled from "styled-components";
import _map from "lodash/map";
import { useTabs } from "../../../common/hooks";
import _head from "lodash/head";

const MenuItem = styled(Menu.Item)``;

const Content = styled(Segment)`
  min-height: 200px;
`;

const Container = styled.div`
  padding: 0 20px;
`;

const TabCount = styled.div``;

const PageTabs = ({ tabs, initTab, children, renderActions }) => {
  const { currentTab, onSwitchTab, onUpdateTabCount, countState } = useTabs({
    initTab: initTab || _head(tabs)
  });
  return (
    <Container>
      <Menu attached="top" tabular>
        {_map(tabs, tab => {
          const { name, label } = tab;
          return (
            <MenuItem
              key={name}
              name={name}
              onClick={() => onSwitchTab(tab)}
              active={tab.name === currentTab.name}
            >
              {label}
              {countState && countState[name] && (
                <TabCount>({countState[name]})</TabCount>
              )}
            </MenuItem>
          );
        })}
        {renderActions && (
          <Menu.Menu position="right">
            <Menu.Item>{renderActions({ currentTab })}</Menu.Item>
          </Menu.Menu>
        )}
      </Menu>
      <Content attached="bottom">
        {children && children({ currentTab, onSwitchTab, onUpdateTabCount })}
      </Content>
    </Container>
  );
};

export default PageTabs;
