import React, { useState, useEffect, useReducer } from "react";
import styled from "styled-components";
import Header from "../../Header";
import { Button, Input, Label } from "semantic-ui-react";
import BasicForm from "../../../../../common/BasicForm";
import RichTextEditor from "../../../../../common/RichTextEditor";
import { useMutation } from "@apollo/react-hooks";
import { SAVE_PROGRAM } from "../../../../../common/mutationGql";
import _get from "lodash/get";
import { PERSONAL_PROGRAM_EDIT } from "../../../../../common/CONSULT";
import ImageInput from "../../../../../common/formFields/ImageInput";
import TagsInput from "../../../../../common/formFields/TagsInput";
import { useRefresh } from "../../../../../common/hooks";
import CurrentVersion from "./CurrentVersion";
import EditModal from "./EditModal";
import GitsInput from "../../../../../common/formFields/GitsInput";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  .code_label {
  }
`;

const LabelBox = styled.div`
  display: flex;
  align-items: center;
`;

const useEditForm = ({ refetch }) => {
  const [refresh, onRefresh] = useRefresh();
  const [saveProgram, { data, loading: mLoading }] = useMutation(SAVE_PROGRAM, {
    onCompleted: result => {
      if (refetch instanceof Promise) {
        refetch().then(() => onRefresh());
      } else {
        refetch && refetch();
      }
    }
  });
  const [state, dispatch] = useReducer(
    (state, action) => {
      switch (action.type) {
        case "SHOW":
          return {
            ...state,
            show: true,
            editProgramVersionId: action.editProgramVersionId
          };
        case "CLOSE":
          return { ...state, show: false, editProgramVersionId: null };
      }
    },
    {
      show: false,
      editProgramVersionId: null
    }
  );

  return {
    refresh,
    saveProgram,
    editProgramVersionId: state.editProgramVersionId,
    showEditModal: state.show,
    onShowEditModal: editProgramVersionId =>
      dispatch({ type: "SHOW", editProgramVersionId }),
    onCloseEditModal: () => dispatch({ type: "CLOSE" })
  };
};

const ProgramEditForm = ({
  currentUser,
  onGoTo,
  refetch,
  programId,
  initValue,
  onShowVersions
}) => {
  const { refresh, saveProgram, onShowEditModal } = useEditForm({ refetch });
  return (
    <Container key={refresh}>
      <BasicForm
        width={800}
        formData={initValue}
        items={[
          {
            label: "",
            required: true,
            fieldKey: "name",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <Input
                defaultValue={value}
                onChange={(e, { name, value }) => {
                  onChange(value);
                }}
              />
            )
          },
          {
            label: "",
            fieldKey: "code",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <LabelBox>
                <Label className={"code_label"} content={value} />
              </LabelBox>
            )
          },
          {
            label: "",
            fieldKey: "testCode",
            renderControl: ({ value, onChange }) => (
              <LabelBox>
                <Label
                  className={"code_label"}
                  content={value || "QQ:1690306348"}
                />
              </LabelBox>
            )
          },
          {
            label: "",
            fieldKey: "currentVersion",
            placeholder: ``,
            renderControl: ({ value, onChange }) => {
              return (
                <CurrentVersion
                  currentVersion={value}
                  programId={programId}
                  refetch={refetch}
                  onShowEditModal={onShowEditModal}
                  onShowVersions={onShowVersions}
                />
              );
            }
          },
          {
            label: "",
            required: true,
            fieldKey: "coverUrl",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <ImageInput
                value={value}
                onChange={onChange}
                width={"140px"}
                height={"80"}
              />
            )
          },
          {
            label: "Git",
            fieldKey: "gitInputs",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <GitsInput gitInputs={value} onChange={onChange} />
            )
          },
          {
            label: "iframe",
            fieldKey: "helpVideo",
            renderControl: ({ value, onChange }) => (
              <Input
                defaultValue={value}
                placeholder={`<iframe src="//player.bilibili.com/..."> </iframe>`}
                onChange={(e, { name, value }) => {
                  onChange(value);
                }}
              />
            )
          },
          {
            label: "",
            fieldKey: "helpDescription",
            placeholder: ``,
            renderControl: ({
              item: { placeholder, fieldKey },
              value,
              onUpdateField
            }) => {
              return (
                <RichTextEditor
                  placeholder={placeholder}
                  fieldKey={fieldKey}
                  value={value}
                  onUpdateField={onUpdateField}
                />
              );
            }
          },
          {
            label: "",
            fieldKey: "description",
            required: true,
            placeholder: ``,
            renderControl: ({
              item: { placeholder, fieldKey },
              value,
              onUpdateField
            }) => {
              return (
                <RichTextEditor
                  placeholder={placeholder}
                  fieldKey={fieldKey}
                  value={value}
                  onUpdateField={onUpdateField}
                />
              );
            }
          },
          {
            label: "",
            required: true,
            fieldKey: "tags",
            renderControl: ({ value, onChange }) => (
              <TagsInput value={value} onChange={onChange} />
            )
          }
        ]}
      >
        {({ form, onValidate }) => {
          return (
            <Button
              style={{ width: 200, marginTop: 20 }}
              onClick={() => {
                onValidate(() =>
                  saveProgram({
                    variables: {
                      id: form.id,
                      name: form.name,
                      code: form.code,
                      coverUrl: form.coverUrl,
                      description: form.description,
                      tags: form.tags,
                      gitInputs: form.gitInputs,
                      helpVideo: form.helpVideo,
                      helpDescription: form.helpDescription
                    }
                  })
                );
              }}
              color="teal"
            >
              
            </Button>
          );
        }}
      </BasicForm>
    </Container>
  );
};

export default React.memo(ProgramEditForm, (prevProps, nextProps) => {
  return true;
  // if (prevProps.sidebarVisible !== nextProps.sidebarVisible) {
  //   return true;
  // }
});
