import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PaginationQueryList from "../../../../../common/PaginationQueryList";
import { GET_COMMENTS } from "../../../../../common/queryGql";
import _map from "lodash/map";
import CommentItem from "./CommentItem";
import LoadingWrapper from "../../../../../common/LoadingWapper";
import Empty from "../../../../../common/Empty";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 14px;
  min-height: 200px;
`;

const RelatedComments = ({ currentUser }) => {
  return (
    <PaginationQueryList
      gql={GET_COMMENTS}
      searchFields={{
        isUserRelated: true
      }}
      paginationSize={"mini"}
      paginationSecondary={true}
      dataKey={"comments"}
      hidePaginationInSinglePage={true}
      limit={20}
    >
      {({ loading, count, items, refetch }) => {
        if (count <= 0) {
          return <Empty text={""} />;
        }
        return (
          <Container>
            <LoadingWrapper loading={loading}>
              {_map(items, (item, index) => {
                return (
                  <CommentItem
                    key={"related_comment_" + item.id}
                    item={item}
                    isLastOne={index + 1 === items.length}
                  />
                );
              })}
            </LoadingWrapper>
          </Container>
        );
      }}
    </PaginationQueryList>
  );
};

export default RelatedComments;
