import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { BOOK, CHAPTER, OBJ_TYPES } from "../../../../../common/CONSULT";
import { Icon } from "semantic-ui-react";
import { MAIN_RED } from "../../../../../common/Styles";
import { useSwitchFollow } from "../../../../../common/hooks";

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 660px;
  margin-bottom: 10px;
  cursor: pointer;
  border-bottom: 1px solid #cecece;
  padding: 10px 0px;
  &:hover {
    border-bottom: 1px solid pink;
    .chapter_name {
      color: ${MAIN_RED};
    }
  }
`;

const LeftBox = styled.div`
  display: flex;
  align-items: center;
`;
const RightBox = styled.div`
  display: flex;
  align-items: center;
`;

const ChapterName = styled.div`
  font-size: 20px;
  font-weight: bolder;
  margin-right: 20px;
`;

const BookName = styled.div`
  cursor: pointer;
  color: gray;
  &:hover {
    color: lightskyblue;
  }
`;

const UpdatedDate = styled.div`
  font-size: 12px;
  color: #cecece;
  margin-right: 10px;
`;

const RemoveButton = styled(Icon)`
  color: gray;
  &:hover {
    color: lightcoral;
  }
`;

const BookmarkItem = ({ chapter, onGoTo, refetchFollows, noActions }) => {
  const { onSwitchFollow } = useSwitchFollow({
    currentFollowed: true,
    objId: chapter.id,
    objType: OBJ_TYPES.CHAPTER,
    onSwitchCompleted: () => refetchFollows && refetchFollows(),
    successMsg: { unFollowed: "" },
    errorMsg: { unFollowed: "" }
  });
  const {
    id,
    title,
    lastUpdated,
    book: { id: bookId, name }
  } = chapter;
  return (
    <Container onClick={() => (onGoTo ? onGoTo(`${CHAPTER}${id}`) : null)}>
      <LeftBox>
        <ChapterName className={"chapter_name"}>{title}</ChapterName>
        <BookName
          onClick={e => {
            onGoTo(`${BOOK}${bookId}`);
            e.stopPropagation();
          }}
        >
          [{name}]
        </BookName>
      </LeftBox>
      <RightBox>
        <UpdatedDate>{lastUpdated}</UpdatedDate>
        {!noActions && (
          <RemoveButton
            onClick={e => {
              onSwitchFollow();
              e.stopPropagation();
            }}
            name={"trash alternate outline"}
          />
        )}
      </RightBox>
    </Container>
  );
};

export default BookmarkItem;
