import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Header from "../Header";
import FollowsQuery from "../FollowsQuery";
import { OBJ_TYPES } from "../../../../common/CONSULT";
import { followDataToJson } from "../../../../utils/jsonUtils";
import BookmarkItem from "./segaments/BookmarkItem";
import PersistStorage from "../../../../utils/PersistStorage";

const Container = styled.div`
  width: 100%;
`;

const ChaptersWrapper = styled.div`
  padding: 0 22px;
`;

const Chapter = ({ currentUser, onGoTo }) => {
  const markedChanged = PersistStorage.getItem("markChanged");
  return (
    <Container>
      <Header title={""} />
      <ChaptersWrapper>
        <FollowsQuery
          refetchId={"personal_chapter_refetch"}
          userId={currentUser.id}
          emptyText={""}
          type={OBJ_TYPES.CHAPTER}
          columns={1}
          refetchNow={markedChanged}
          beforeRefetch={() => PersistStorage.setItem("markChanged", false)}
        >
          {({ item, refetchFollows }) => {
            const chapter = followDataToJson(item.followedObj);
            return (
              <BookmarkItem
                chapter={chapter}
                onGoTo={onGoTo}
                refetchFollows={refetchFollows}
              />
            );
          }}
        </FollowsQuery>
      </ChaptersWrapper>
    </Container>
  );
};

export default Chapter;
