import React from "react";
import styled from "styled-components";
import _find from "lodash/find";

import { Mutation } from "react-apollo";
import { IMPORT_TYPE } from "../../../../../common/CONSULT";
import { PROGRAM_VERSION_STATUS } from "../../../../../common/OPTIONS";
import { UPDATE_PROGRAM_VERSION_STATUS } from "../../../../../common/mutationGql";
import { Button } from "semantic-ui-react";
import { Link } from "react-router-dom";

const Container = styled.div`
  background-color: white;
  display: flex;
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid #efefef;
  .link_btn {
    display: none;
  }
  &:hover {
    .link_btn {
      display: block;
    }
  }
`;

const ContentContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
`;
const Title = styled.div`
  color: black;
  font-weight: bold;
  font-size: 16px;
  text-align: left;
  flex: 1;
`;

const SubTitle = styled.span`
  color: darkgray;
  font-size: 14px;
  text-align: left;
  margin-left: 20px;
`;
const Description = styled.div`
  margin-top: 10px;
  color: #cecece;
  font-size: 16px;
  text-align: left;
`;
const OperationsContainer = styled.div`
  min-width: 100px;
  padding: 0 4px;
  display: flex;
  align-items: center;
  justify-content: space-around;
`;
const Head = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const Date = styled.div`
  color: #cecece;
  font-size: 16px;
  margin-right: 20px;
  line-height: 18px;
`;

const Creator = styled.div`
  font-size: 14px;
  line-height: 18px;
`;

const HeadRight = styled.div`
  display: flex;
  justify-content: flex-end;
`;

const StatusBox = styled.div`
  color: white;
  background-color: ${props => props.bgColor};
  padding: 2px 6px;
  border-radius: 10px;
  margin-left: 10px;
  text-align: center;
`;

const StatusLabel = ({ status }) => {
  const statusItem = _find(PROGRAM_VERSION_STATUS, s => s.value === status);
  return <StatusBox bgColor={statusItem.bColor}>{statusItem.label}</StatusBox>;
};

const ProgramVersion = ({ item, programId, totalCount, items, refetch }) => {
  const {
    name,
    dateCreated,
    submitter,
    description,
    status,
    importType,
    codeFile,
    id
  } = item;

  return (
    <Container>
      <ContentContainer>
        <Head>
          <Title>
            {name}
            <SubTitle>[{IMPORT_TYPE[importType]}]</SubTitle>
          </Title>
          <HeadRight>
            <Date>{dateCreated}</Date>
            <Creator>By{submitter.username}</Creator>
            <StatusLabel status={status} />
          </HeadRight>
        </Head>

        <Description>{description}</Description>
      </ContentContainer>
      <Mutation
        mutation={UPDATE_PROGRAM_VERSION_STATUS}
        onCompleted={() => refetch()}
      >
        {onUpdateStatus => {
          return (
            <OperationsContainer>
              {status === "Draft" && (
                <Button
                  onClick={() =>
                    onUpdateStatus({
                      variables: { status: "Awaiting", id: item.id }
                    })
                  }
                  color="teal"
                  size={"mini"}
                >
                  {""}
                </Button>
              )}
              <Link
                className={"link_btn"}
                to={`/program/${programId}/${item.id}`}
              >
                {status !== "Published" ? (
                  <Button color="orange" circular basic size={"mini"}>
                    {""}
                  </Button>
                ) : (
                  <Button color="blue" circular basic size={"mini"}>
                    {""}
                  </Button>
                )}
              </Link>
            </OperationsContainer>
          );
        }}
      </Mutation>
    </Container>
  );
};

export default ProgramVersion;
