import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PaginationQueryList from "../../../../../common/PaginationQueryList";
import { GET_PROBLEMS } from "../../../../../common/queryGql";
import _map from "lodash/map";
import ProblemItem from "../../../../../common/item/ProblemItem";
import Empty from "../../../../../common/Empty";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 14px;
  min-height: 200px;
`;

const MyProblems = ({ currentUser }) => {
  return (
    <PaginationQueryList
      gql={GET_PROBLEMS}
      searchFields={{
        creatorId: currentUser.id,
        statuses: ["Awaiting", "Published", "Accepted", "Rejected"]
      }}
      paginationSize={"mini"}
      paginationSecondary={true}
      dataKey={"problems"}
      limit={10}
    >
      {({ loading, count, items, refetch }) => {
        if (count <= 0) {
          return <Empty text={""} />;
        }
        return (
          <Container>
            {_map(items, item => {
              return (
                <ProblemItem key={"my_problem_" + item.id} problem={item} />
              );
            })}
          </Container>
        );
      }}
    </PaginationQueryList>
  );
};

export default MyProblems;
