import React, { useState } from "react";
import styled from "styled-components";
import { getOptionByKey } from "../../../../../utils/optionsUtils";
import { PROGRAM_STATUS } from "../../../../../common/OPTIONS";
import { Icon, Popup, Button } from "semantic-ui-react";
import { useUpdateProgramStatus } from "../../../../../common/hooks";
import UpdateStatusButton from "./UpdateStatusButton";
import Space from "../../../../../common/Space";

const Container = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 26px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.3);
  padding-right: 10px;
`;

const Cover = styled.img`
  width: 200px; 
  height: 140px;
`;

const Header = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 10px;
`;

const Title = styled.div`
  font-size: 18px;
  font-weight: bold;
`;

const Content = styled.div`
  display: flex;
  flex-direction: column;
  padding: 10px 10px;
  flex: 1;
  height: 140px;
  justify-content: space-between;
`;

const ActionsWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const Status = styled.div`
  background-color: ${props => props.bColor};
  color: white;
  width: 80px;
  font-size: 16px;
  line-height: 28px;
  border-radius: 20px;
  height: 28px;
`;

const ContentRow = styled.div`
  display: flex;
`;

const Code = styled.div`
  font-size: 14px;
  color: gray;
`;

const ContentFooter = styled.div`
  display: flex;
  align-items: center;
`;

const DateFields = styled.div`
  color: #cecece;
  margin-left: 30px;
`;

const VersionCount = styled.div`
  color: gray;
  margin-left: 20px;
`;

const CountItemBox = styled.div`
  color: gray;
  margin-left: 20px;
`;

const useProgramItem = ({ programItem }) => {
  const [program, setProgram] = useState(programItem);
  const { mLoading, onUpdateProgramStatus } = useUpdateProgramStatus({
    programId: program.id,
    onUpdateStatusCompleted: status => {
      setProgram({ ...program, status });
    }
  });
  return { program, mLoading, onUpdateProgramStatus };
};

const CurrentVersionBox = styled.div`
  margin-left: 40px;
  border: 1px solid lightskyblue;
  color: darkgray;
  padding: 2px 6px;
`;

const CurrentVersion = ({ currentVersion }) => {
  if (!currentVersion) {
    return null;
  }
  const { id, name, versionCode } = currentVersion;
  return (
    <CurrentVersionBox>
      {name}&nbsp;&nbsp;&nbsp;&nbsp;{versionCode}
    </CurrentVersionBox>
  );
};

const ProgramItem = ({
  program: programItem,
  noActions,
  onLinkTo,
  onEdit,
  onToProgram
}) => {
  const { program, onUpdateProgramStatus, mLoading } = useProgramItem({
    programItem
  });
  const {
    id,
    dateCreated,
    lastUpdated,
    upCount,
    followerCount,
    name,
    status,
    code,
    cover: { url },
    currentVersion,
    versionCount
  } = program;
  const { label, bColor } = getOptionByKey(PROGRAM_STATUS, "value", status);
  return (
    <Container>
      <Cover src={url} />
      <Content>
        <Header>
          <Title>{name}</Title>
        </Header>
        <ContentRow>
          <Code>{code}</Code>
          <VersionCount>{versionCount}</VersionCount>
          <Popup
            content={""}
            trigger={
              <CountItemBox>
                <Icon name={"heart"} />
                {upCount}
              </CountItemBox>
            }
          />

          <Popup
            content={""}
            trigger={
              <CountItemBox>
                <Icon name={"star"} />
                {followerCount}
              </CountItemBox>
            }
          />
          <CurrentVersion currentVersion={currentVersion} />
        </ContentRow>
        <ContentFooter>
          <Status bColor={bColor}>{label}</Status>
          <DateFields>{dateCreated}</DateFields>
          <DateFields>{lastUpdated}</DateFields>
        </ContentFooter>
      </Content>
      {noActions ? null : (
        <ActionsWrapper>
          <UpdateStatusButton
            status={status}
            onUpdateProgramStatus={onUpdateProgramStatus}
          />
          <Space width={"10px"} />
          <Button size={"mini"} basic color="teal" onClick={onEdit}>
            
          </Button>
          <Space width={"10px"} />
          {programItem.currentVersion && (
            <Button size={"mini"} basic color="blue" onClick={onToProgram}>
              
            </Button>
          )}
        </ActionsWrapper>
      )}
    </Container>
  );
};

export default ProgramItem;
