import React from "react";
import styled from "styled-components";
import { Rating } from "semantic-ui-react";

const Container = styled.div`
  &:hover {
    .cover_img {
      box-shadow: 0 8px 20px #bbb;
    }
    .program_name {
      color: #fb6b84;
      font-size: 16px;
    }
  }
`;

const Cover = styled.img`
  width: 240px;
  height: 140px;
`;

const Name = styled.div`
  margin-top: 4px;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  margin-bottom: 4px;
`;

const Footer = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const RatingCount = styled.div`
  .rating {
    pointer-events: none;
    ::selection {
      color: transparent;
    }
  }
`;

const ProgramItem = ({ program }) => {
  const {
    id,
    cover: { url },
    name,
    scoreCount = 0,
    averageScore = 0
  } = program;
  return (
    <Container>
      <Cover className={"cover_img"} src={url} />
      <Name className={"program_name"}>{name}</Name>
      <Footer>
        <Rating disabled defaultRating={averageScore} maxRating={5} />
        <RatingCount>{scoreCount}</RatingCount>
      </Footer>
    </Container>
  );
};

export default ProgramItem;
