import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Empty from "./Empty";
import { PERSONAL, PERSONAL_PAGES } from "../../common/CONSULT";
import Menus from "./pages/Menus";
import UserSettings from "./pages/userSettings/UserSettings";
import Program from "./pages/program/Program";
import { getPersonPage } from "../../utils/pathUtils";
import Book from "./pages/book/Book";
import Blog from "./pages/blog/Blog";
import Chapter from "./pages/chapter/Chapter";
import Comment from "./pages/comment/Comment";
import Follower from "./pages/follower/Follower";
import Up from "./pages/up/Up";
import Problem from "./pages/problem/Problem";
import ProgramCreate from "./pages/program/create/ProgramCreate";
import ProgramEdit from "./pages/program/eidt/ProgramEdit";
const Container = styled.div`
  display: flex;
  width: 1300px;
  margin-top: 18px;
`;

const PagePanel = styled.div`
  flex: 1;
`;

const MenusBox = styled.div`
  display: flex;
  flex-direction: column;
  height: 600px;
  padding: 0px 4px;
  border: 1px solid rgba(34, 36, 38, 0.15);
  align-items: center;
  .menu_item {
    font-size: 16px !important;
    color: #222222 !important;
    padding-top: 14px !important;
  }
`;

const getPageByName = (page, currentUser, onGoTo) => {
  const commonProps = { onGoTo, currentUser };
  switch (page) {
    case PERSONAL_PAGES.USER_SETTINGS:
      return <UserSettings />;
    case PERSONAL_PAGES.PROGRAM:
      return <Program {...commonProps} />;
    case PERSONAL_PAGES.PROGRAM_CREATE:
      return <ProgramCreate {...commonProps} />;
    case PERSONAL_PAGES.PROGRAM_EDIT:
      return <ProgramEdit {...commonProps} />;
    case PERSONAL_PAGES.FOLLOWER:
      return <Follower {...commonProps} />;
    case PERSONAL_PAGES.UP:
      return <Up {...commonProps} />;
    case PERSONAL_PAGES.BLOG:
      return <Blog {...commonProps} />;
    case PERSONAL_PAGES.COMMENT:
      return <Comment {...commonProps} />;
    case PERSONAL_PAGES.BOOK:
      return <Book {...commonProps} />;
    case PERSONAL_PAGES.CHAPTER:
      return <Chapter {...commonProps} />;
    case PERSONAL_PAGES.PROBLEM:
      return <Problem {...commonProps} />;
    default:
      return null;
  }
};

const usePersonalPage = ({ currentUser, onGoTo }) => {
  const page = getPersonPage();
  console.log("page___________________", page);

  const content = getPageByName(page, currentUser, onGoTo);
  return { page, onSwitchPage: page => onGoTo(PERSONAL + page), content };
};

const PersonalPage = ({ currentUser, onGoTo }) => {
  const { page, onSwitchPage, content } = usePersonalPage({
    currentUser,
    onGoTo
  });
  const commonProps = { page, onSwitchPage };
  return currentUser ? (
    <Container>
      <MenusBox>
        <Menus commonProps={commonProps} />
      </MenusBox>
      <PagePanel>{content}</PagePanel>
    </Container>
  ) : (
    <Empty />
  );
};

export default PersonalPage;
