import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useMe } from "../../../../common/hooks";
import BasicForm from "../../../../common/BasicForm";
import { Button, Input } from "semantic-ui-react";
import { useMutation } from "@apollo/react-hooks";
import { SAVE_CURRENT_USER } from "../../../../common/mutationGql";
import ToastHint from "../../../../common/ToastHint";
import Loading from "../../../../common/Loading";
import Header from "../Header";
import BindPhoneInput from "../../../../common/formFields/BindPhoneInput";
import BindEmailInput from "../../../../common/formFields/BindEmailInput";
import BindPhoneModal from "./BindPhoneModal";
import BindEmailModal from "./BindEmailModal";
import ImageInput from "../../../../common/formFields/ImageInput";
import _get from "lodash/get";
import ResetPasswordInput from "../../../../common/formFields/ResetPasswordInput";
import ResetPasswordModal from "./ResetPasswordModal";

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const useSettings = () => {
  const [showBindPhoneModal, setShowBindPhoneModal] = useState(false);
  const [showBindEmailModal, setShowBindEmailModal] = useState(false);
  const [showResetPasswordModal, setResetPasswordModal] = useState(false);
  const { me, loading, refetch } = useMe();
  const [onSaveCurrentUser, {}] = useMutation(SAVE_CURRENT_USER, {
    onCompleted: () => {
      //refetch && refetch();
      ToastHint.showSuccess("");
    },
    onError: error => {
      console.log("errorGGGG__________________________", error);
      ToastHint.showFailure("");
    }
  });
  return {
    me,
    loading,
    refetch,
    onSaveCurrentUser,
    showBindPhoneModal,
    setShowBindPhoneModal,
    showBindEmailModal,
    setShowBindEmailModal,
    showResetPasswordModal,
    setResetPasswordModal
  };
};

const UserSettings = () => {
  const {
    me,
    loading,
    refetch,
    onSaveCurrentUser,
    showBindPhoneModal,
    setShowBindPhoneModal,
    showBindEmailModal,
    setShowBindEmailModal,
    showResetPasswordModal,
    setResetPasswordModal
  } = useSettings();
  if (!me) {
    return <Loading />;
  }
  return (
    <Container>
      <Header title={""} />
      <BasicForm
        loading={loading}
        width={400}
        formData={{ ...me, avatarUrl: _get(me, "avatar.url") }}
        items={[
          {
            label: "",
            fieldKey: "avatarUrl",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <ImageInput
                isAvatar
                value={value}
                onChange={onChange}
                width={120}
                height={120}
              />
            )
          },
          {
            label: "",
            required: true,
            fieldKey: "username",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <Input
                defaultValue={value}
                onChange={(e, { name, value }) => {
                  onChange(value);
                }}
              />
            )
          },
          {
            label: "",
            required: true,
            fieldKey: "nickname",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <Input
                defaultValue={value}
                onChange={(e, { name, value }) => {
                  onChange(value);
                }}
              />
            )
          },
          {
            label: "",
            fieldKey: "phone",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <BindPhoneInput
                value={value}
                onBindCompleted={refetch}
                onBind={() => setShowBindPhoneModal(true)}
              />
            )
          },
          {
            label: "",
            fieldKey: "email",
            placeholder: ``,
            renderControl: ({ value, onChange }) => (
              <BindEmailInput
                value={value}
                onBindCompleted={refetch}
                onBind={() => setShowBindEmailModal(true)}
              />
            )
          },
          {
            label: "",
            fieldKey: "password",
            renderControl: ({ value, onChange }) => (
              <ResetPasswordInput onReset={() => setResetPasswordModal(true)} />
            )
          }
        ]}
      >
        {({ form, onValidate }) => {
          return (
            <Button
              style={{ width: 200, marginTop: 20 }}
              onClick={() => {
                onValidate(() =>
                  onSaveCurrentUser({
                    variables: {
                      username: form.username,
                      nickname: form.nickname,
                      email: form.email,
                      fullName: form.fullName,
                      phone: form.phone,
                      avatarUrl: form.avatarUrl
                    }
                  })
                );
              }}
              color="teal"
            >
              
            </Button>
          );
        }}
      </BasicForm>
      <BindPhoneModal
        title={""}
        open={showBindPhoneModal}
        onRefetch={refetch}
        onHide={() => setShowBindPhoneModal(false)}
      />
      <BindEmailModal
        title={""}
        onRefetch={refetch}
        open={showBindEmailModal}
        onHide={() => setShowBindEmailModal(false)}
      />
      <ResetPasswordModal
        title={""}
        email={me.email}
        phone={me.phone}
        open={showResetPasswordModal}
        onRefetch={refetch}
        onHide={() => setResetPasswordModal(false)}
      />
    </Container>
  );
};

export default UserSettings;
