import React, { useState, useEffect } from "react";
import styled from "styled-components";
import UpsQuery from "./UpsQuery";
import { followDataToJson } from "../../../../utils/jsonUtils";
import ObjItem from "../ObjItem";

const Ups = ({ currentUser, types, type, emptyText }) => {
  return (
    <UpsQuery
      userId={currentUser.id}
      emptyText={emptyText}
      columns={1}
      types={types}
      type={type}
    >
      {({ item, isLastRow }) => {
        const obj = followDataToJson(item.uppedObj);
        return (
          <ObjItem obj={obj} objType={item.objType} isLastOne={isLastRow} />
        );
      }}
    </UpsQuery>
  );
};

export default Ups;
