import React, { useState, useEffect } from "react";
import EditModal from "./EditModal";

const EditModalTrigger = ({
  refetch,
  programId,
  editProgramVersionId,
  children
}) => {
  const [show, setShow] = useState(false);
  return (
    <span
      onClick={e => {
        e.stopPropagation();
        setShow(true);
      }}
    >
      {children}
      <EditModal
        refetch={refetch}
        open={show}
        programId={programId}
        onClose={e => {
          setShow(false);
          e && e.stopPropagation();
        }}
        programVersionId={editProgramVersionId}
      />
    </span>
  );
};

export default EditModalTrigger;
