import React, { useState, useEffect } from "react";
import styled from "styled-components";
import BasicForm from "../../../../../common/BasicForm";
import { Input, TextArea, Select } from "semantic-ui-react";
import { useMutation } from "@apollo/react-hooks";
import { SAVE_PROGRAM_VERSION } from "../../../../../common/mutationGql";
import FileInput from "../../../../../common/formFields/FileInput";
import _get from "lodash/get";
import _endsWith from "lodash/endsWith";
import { IMPORT_TYPE } from "../../../../../common/CONSULT";
import ModalHint from "../../../../../common/ModalHint";

const Container = styled.div``;

const useEditVersion = ({ form }) => {
  const importType = _get(form, "importType", ".js");
  const [accept, setAccept] = useState(".js");
  const onUpdateAcceptTypeImportType = importType => {
    switch (importType) {
      case "OnlyJs":
        setAccept(".js");
        break;
      case "OnlyHtml":
        setAccept(".html");
        break;
    }
  };
  useEffect(() => {
    onUpdateAcceptTypeImportType(importType);
  }, []);
  return { accept, onUpdateAcceptTypeImportType };
};

const EditVersionForm = ({
  loading,
  initValue,
  programId,
  refetch,
  onCloseModal,
  setLoading
}) => {
  const [saveProgramVersion, { data, loading: mLoading }] = useMutation(
    SAVE_PROGRAM_VERSION,
    {
      onCompleted: result => {
        onCloseModal && onCloseModal();
        refetch && refetch();
      },
      onError: error => {
        setLoading(false);
      }
    }
  );

  const oldCodeUrl = _get(initValue, "codeFile.url");
  const { accept, onUpdateAcceptTypeImportType } = useEditVersion({
    form: initValue
  });
  return (
    <Container>
      <BasicForm
        width={600}
        formData={initValue}
        items={[
          {
            label: "",
            required: true,
            fieldKey: "name",
            renderControl: ({ value, onChange }) => (
              <Input
                placeholder={``}
                defaultValue={value}
                onChange={(e, { name, value }) => {
                  onChange(value);
                }}
              />
            )
          },

          {
            label: "",
            fieldKey: "versionCode",
            required: true,
            renderControl: ({ value, onChange }) => {
              return (
                <Input
                  placeholder={`v1.1.0`}
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    onChange(value);
                  }}
                />
              );
            }
          },
          {
            label: "",
            fieldKey: "importType",
            required: true,
            renderControl: ({ value, onChange, onUpdateField }) => {
              return (
                <Select
                  defaultValue={value}
                  onChange={(e, { name, value }) => {
                    onChange(value);
                    onUpdateAcceptTypeImportType(value);
                    onUpdateField("codeUrl", null);
                  }}
                  options={[
                    {
                      key: IMPORT_TYPE.OnlyJs,
                      value: "OnlyJs",
                      text: IMPORT_TYPE.OnlyJs
                    },
                    {
                      key: IMPORT_TYPE.OnlyHtml,
                      value: "OnlyHtml",
                      text: IMPORT_TYPE.OnlyHtml
                    }
                  ]}
                />
              );
            }
          },
          {
            label: "",
            fieldKey: "codeUrl",
            required: true,
            renderControl: ({ value, onChange }) => {
              return (
                <FileInput
                  value={value}
                  isTmp={true}
                  accept={accept}
                  onChange={onChange}
                  fileData={_get(initValue, "codeFile")}
                />
              );
            }
          },
          {
            label: "",
            fieldKey: "description",
            placeholder: ``,
            required: true,
            renderControl: ({
              item: { placeholder, fieldKey },
              value = "",
              onChange
            }) => {
              return (
                <TextArea
                  onChange={(e, { value }) => onChange(value)}
                  placeholder={placeholder}
                  value={value}
                />
              );
            }
          }
        ]}
      >
        {({ form, onValidate }) => {
          return (
            <div
              id={"save_program_version"}
              onClick={
                mLoading
                  ? null
                  : () => {
                      onValidate(() => {
                        // codeUrl 
                        const codeUrl =
                          oldCodeUrl === form.codeUrl ? "" : form.codeUrl;
                        if (codeUrl && !_endsWith(codeUrl, accept)) {
                          ModalHint.info(
                            ``
                          );
                        } else {
                          saveProgramVersion({
                            variables: {
                              id: form.id,
                              programId,
                              versionCode: form.versionCode,
                              name: form.name,
                              importType: form.importType,
                              // codeUrl 
                              codeUrl:
                                oldCodeUrl === form.codeUrl ? "" : form.codeUrl,
                              description: form.description
                            }
                          });
                          setLoading(true);
                        }
                      });
                    }
              }
              color="teal"
            />
          );
        }}
      </BasicForm>
    </Container>
  );
};

export default EditVersionForm;
