import React, { useState, useEffect } from "react";
import { PERSONAL_PAGES } from "../../../common/CONSULT";
import { Icon, Menu } from "semantic-ui-react";
import styled from "styled-components";

const Title = styled.div`
  font-size: 18px;
  color: #99a2aa;
  font-weight: bold;
  height: 40px;
  line-height: 40px;
`;

const PersonalHead = styled(Menu.Item)`
  border-bottom: 1px solid rgba(34, 36, 38, 0.15) !important;
  margin-bottom: 0 !important;
`;

const MenuItem = ({ label, name, page, onSwitchPage, iconName }) => {
  return (
    <Menu.Item
      className={"menu_item"}
      onClick={() => onSwitchPage(name)}
      active={page.indexOf(name) >= 0}
      name={name}
    >
      <Icon name={iconName} /> {label}
    </Menu.Item>
  );
};

const Menus = ({ commonProps }) => {
  return (
    <Menu secondary style={{ width: 180 }} vertical>
      <PersonalHead style={{ height: 60 }}>
        <Title></Title>
      </PersonalHead>
      <MenuItem
        {...commonProps}
        iconName={"th"}
        name={PERSONAL_PAGES.PROGRAM}
        label={""}
      />
      <MenuItem
        {...commonProps}
        name={PERSONAL_PAGES.BLOG}
        label={""}
        iconName={"blogger"}
      />
      <MenuItem
        {...commonProps}
        name={PERSONAL_PAGES.BOOK}
        label={""}
        iconName={"industry"}
      />
      <MenuItem
        {...commonProps}
        name={PERSONAL_PAGES.CHAPTER}
        label={""}
        iconName={"bookmark"}
      />
      <MenuItem
        {...commonProps}
        name={PERSONAL_PAGES.PROBLEM}
        label={""}
        iconName={"sign-out"}
      />
      <MenuItem
        {...commonProps}
        name={PERSONAL_PAGES.COMMENT}
        label={""}
        iconName={"comments"}
      />
      <MenuItem
        {...commonProps}
        name={PERSONAL_PAGES.UP}
        label={""}
        iconName={"heart"}
      />
      <MenuItem
        {...commonProps}
        name={PERSONAL_PAGES.FOLLOWER}
        label={""}
        iconName={"star"}
      />
      <MenuItem
        {...commonProps}
        iconName={"user md"}
        name={PERSONAL_PAGES.USER_SETTINGS}
        label={""}
      />
    </Menu>
  );
};

export default Menus;
