import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Header from "../Header";
import FollowsQuery from "../FollowsQuery";
import { OBJ_TYPES } from "../../../../common/CONSULT";
import FollowItemWrapper from "../FollowItemWrapper";
import { followDataToJson } from "../../../../utils/jsonUtils";
import BlogItem from "../../../../common/item/BlogItem";

const Container = styled.div`
  width: 100%;
`;

const BlogsWrapper = styled.div`
  width: 100%;
  padding: 0 22px;
`;

const Blog = ({ currentUser, onGoTo }) => {
  return (
    <Container>
      <Header title={""} />
      <BlogsWrapper>
        <FollowsQuery
          userId={currentUser.id}
          emptyText={""}
          type={OBJ_TYPES.BLOG}
          columns={1}
        >
          {({ item, refetchFollows, isLastRow }) => {
            const blog = followDataToJson(item.followedObj);
            return (
              <FollowItemWrapper
                objId={blog.id}
                popupContent={""}
                onSwitchFollowCompleted={refetchFollows}
                successMsg={""}
                errorMsg={""}
              >
                <BlogItem
                  width={800}
                  item={blog}
                  onGoTo={onGoTo}
                  isLastOne={isLastRow}
                />
              </FollowItemWrapper>
            );
          }}
        </FollowsQuery>
      </BlogsWrapper>
    </Container>
  );
};

export default Blog;
