import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Header from "../Header";
import FollowsQuery from "../FollowsQuery";
import { BOOK, OBJ_TYPES } from "../../../../common/CONSULT";
import BookItem from "../../../../common/item/BookItem";
import { followDataToJson } from "../../../../utils/jsonUtils";
import FollowItemWrapper from "../FollowItemWrapper";

const Container = styled.div`
  width: 100%;
`;

const BooksWrapper = styled.div`
  padding: 0 22px;
`;

const Book = ({ currentUser, onGoTo }) => {
  return (
    <Container>
      <Header title={""} />
      <BooksWrapper>
        <FollowsQuery
          emptyText={""}
          userId={currentUser.id}
          type={OBJ_TYPES.BOOK}
          columns={6}
        >
          {({ item, refetchFollows }) => {
            const book = followDataToJson(item.followedObj);
            return (
              <FollowItemWrapper
                objId={book.id}
                popupContent={""}
                onSwitchFollowCompleted={refetchFollows}
                successMsg={""}
                errorMsg={""}
              >
                <BookItem
                  size={0.8}
                  book={book}
                  onToBook={() => onGoTo(`${BOOK}${book.id}`)}
                />
              </FollowItemWrapper>
            );
          }}
        </FollowsQuery>
      </BooksWrapper>
    </Container>
  );
};

export default Book;
