import React from "react";
import styled from "styled-components";
import { MAIN_RED } from "../Styles";

const Container = styled.div`
  width: ${props => props.size * 150}px;
  display: flex;
  color: gray;
  cursor: pointer;
  &:hover {
    color: ${MAIN_RED};
    .cover {
      box-shadow: 0 8px 20px #bbb;
      -webkit-box-shadow: 0 8px 20px #bbb;
    }
  }
  flex-direction: column;
  align-items: center;

  margin-left: 10px;
  margin-right: 10px;
`;

const Cover = styled.img`
  width: ${props => props.size * 150}px;
  height: ${props => props.size * 206}px;
  border: 1px solid #cecece;
`;

const BookName = styled.div`
  margin-top: 4px;
  margin-bottom: 6px;
  width: 100px;
`;

const BookItem = ({ book, size = 1, onToBook }) => {
  const {
    id,
    name,
    cover: { url: imageUrl },
    creator: { id: creatorId, nickname }
  } = book;
  return (
    <Container onClick={() => (onToBook ? onToBook(id) : null)} size={size}>
      <Cover size={size} className={"cover"} src={imageUrl} />
      <BookName size={size}>{name}</BookName>
    </Container>
  );
};

export default BookItem;
