import React, { useEffect } from "react";
import styled from "styled-components";

const Container = styled.div``;

const RefetchHandle = ({ id, refetch, refetchOnce }) => {
  useEffect(() => {
    if (refetchOnce) {
      refetch && refetch();
    }
  }, []);
  return (
    <Container
      id={id}
      onClick={() => {
        refetch();
      }}
    />
  );
};

export default RefetchHandle;
