import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PageTabs from "../PageTabs";
import Header from "../Header";
import MyPrograms from "./segaments/MyPrograms";
import FollowPrograms from "./segaments/FollowPrograms";
import CreateProgramButton from "./segaments/CreateProgramButton";

const Container = styled.div`
  width: 100%;
`;

const PROGRAM_TABS = [
  { name: "Follow", label: "" },
  { name: "My", label: "" }
];

const PageContent = ({ currentTab, currentUser, onGoTo, onUpdateTabCount }) => {
  switch (currentTab.name) {
    case "Follow":
      return (
        <FollowPrograms
          currentUser={currentUser}
          onUpdateTabCount={count => onUpdateTabCount(currentTab.name, count)}
        />
      );
    case "My":
      return (
        <MyPrograms
          currentUser={currentUser}
          onGoTo={onGoTo}
          onUpdateTabCount={count => onUpdateTabCount(currentTab.name, count)}
        />
      );
  }
};

const Program = ({ currentUser, onGoTo }) => {
  return (
    <Container>
      <Header title={""} />
      <PageTabs
        tabs={PROGRAM_TABS}
        renderActions={() => <CreateProgramButton onGoTo={onGoTo} />}
      >
        {({ currentTab, onUpdateTabCount }) => {
          return (
            <PageContent
              currentTab={currentTab}
              currentUser={currentUser}
              onGoTo={onGoTo}
              onUpdateTabCount={onUpdateTabCount}
            />
          );
        }}
      </PageTabs>
    </Container>
  );
};

export default Program;
