import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PageTabs from "../PageTabs";
import { OBJ_TYPES } from "../../../../common/CONSULT";
import Header from "../Header";
import AllUps from "./segaments/AllUps";
import ProgramUps from "./segaments/ProgramUps";
import BlogUps from "./segaments/BlogUps";
import BookUps from "./segaments/BookUps";
import ChapterUps from "./segaments/ChapterUps";
import CommentUps from "./segaments/CommentUps";

const Container = styled.div`
  width: 100%;
`;

const UP_TABS = [
  { name: "All", label: "" },
  { name: OBJ_TYPES.PROGRAM, label: "" },
  { name: OBJ_TYPES.BLOG, label: "" },
  { name: OBJ_TYPES.BOOK, label: "" },
  { name: OBJ_TYPES.CHAPTER, label: "" },
  { name: OBJ_TYPES.COMMENT, label: "" }
];

const PageContent = ({ currentTab, currentUser }) => {
  switch (currentTab.name) {
    case "All":
      return <AllUps currentUser={currentUser} />;
    case OBJ_TYPES.PROGRAM:
      return <ProgramUps currentUser={currentUser} />;
    case OBJ_TYPES.BLOG:
      return <BlogUps currentUser={currentUser} />;
    case OBJ_TYPES.BOOK:
      return <BookUps currentUser={currentUser} />;
    case OBJ_TYPES.CHAPTER:
      return <ChapterUps currentUser={currentUser} />;
    case OBJ_TYPES.COMMENT:
      return <CommentUps currentUser={currentUser} />;
    case OBJ_TYPES.PROBLEM:
      return null;
  }
};

const Up = ({ currentUser }) => {
  return (
    <Container>
      <Header title={""} />
      <PageTabs tabs={UP_TABS}>
        {({ currentTab }) => {
          return (
            <PageContent currentTab={currentTab} currentUser={currentUser} />
          );
        }}
      </PageTabs>
    </Container>
  );
};

export default Up;
