import React, { useState, useEffect } from "react";
import { message, Upload } from "antd";
import styled from "styled-components";
import { useFileInput } from "../hooks";
import { LoadingOutlined, PictureOutlined } from "@ant-design/icons";
import { Icon } from "semantic-ui-react";

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  .uploader {
    display: flex;
    align-items: center;

    > .ant-upload {
   
      border-radius: ${props => (props.isAvatar ? "50%" : "")}
      width: ${props => (props.width ? props.width : "128px")};
      height: ${props => (props.height ? props.height : "128px")};
    }
  }
`;

function beforeUpload(file) {
  const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
  if (!isJpgOrPng) {
    message.error("You can only upload JPG/PNG file!");
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error("Image must smaller than 2MB!");
  }
  return isJpgOrPng && isLt2M;
}

const UploadBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const IconBox = styled.div`
  font-size: 30px;
`;

const ImageInput = ({ isTmp, onChange, value, width, height, isAvatar }) => {
  const { loading, imageUrl, actionUrl, handleChange } = useFileInput({
    isTmp,
    value,
    onChange
  });
  const uploadButton = (
    <UploadBox>
      <IconBox>
        {loading ? (
          <LoadingOutlined />
        ) : isAvatar ? (
          <Icon size={"big"} name={"user outline"} />
        ) : (
          <PictureOutlined />
        )}
      </IconBox>
      {!isAvatar && <div style={{ marginTop: 2 }}></div>}
    </UploadBox>
  );
  return (
    <Container width={width} height={height} isAvatar={isAvatar}>
      <Upload
        name="file"
        listType="picture-card"
        className="uploader"
        showUploadList={false}
        action={actionUrl}
        beforeUpload={beforeUpload}
        onChange={handleChange}
      >
        {imageUrl ? (
          <img
            src={imageUrl}
            alt="avatar"
            style={{
              width: "100%",
              height: "100%",
              borderRadius: isAvatar ? "50%" : ""
            }}
          />
        ) : (
          uploadButton
        )}
      </Upload>
    </Container>
  );
};

export default ImageInput;
